.more {
	background-color : $dark;
}

.more__grid {
	display               : grid;
	grid-gap              : 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		align-items           : center;
		grid-gap              : 40px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.more__title {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/13;
	}
}

.more__item {
	grid-column : 4 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 4 span;
	}

	.b-btn--brown {
		width : 100%;
	}
}

.housing-item--single {
	margin-bottom : 20px;

	.housing-item__footer {
		padding : 22px 22px;
	}

	.housing-item__row {
		display         : flex;
		flex-direction  : column;
		justify-content : space-between;
		min-height      : 168px;
	}

	.housing-item__name {
		align-items    : flex-start;
		display        : flex;
		flex-direction : column;
		flex-shrink    : 0;

		h4 {
			color       : $cream;
			font-size   : 16px;
			font-weight : 400;
			line-height : 21px;
		}
	}

	.housing-item__icons {
		align-items : center;
		display     : flex;
		gap         : 3px;
		margin-left : 0;
		margin-top  : 12px;

		img {
			height : 15px;
			width  : 15px;
		}
	}

	.housing-item__text {
		color       : rgba(242, 233, 225, 0.7);
		font-size   : 12px;
		font-weight : 400;
		line-height : 15px;
		max-width   : 270px;
	}

	.housing-item__group {
		align-items : center;
		color       : $cream;
		display     : flex;
		flex-shrink : 0;
		font-size   : 16px;
		font-weight : 400;
		line-height : 21px;


		span {
			padding-left : 5px;
		}
	}

	.housing-item__about {
		align-items    : flex-start;
		display        : flex;
		flex-direction : column;
		gap            : 12px;
		width          : 100%;
	}

	.housing-item__info {
		align-items     : center;
		display         : flex;
		gap             : 16px;
		justify-content : flex-end;
		width           : 100%;
	}
}

.icons-group {
	align-items : center;
	display     : flex;
	position    : relative;

	&:not(:first-child) {
		padding-left : 14px;

		&:before {
			background-image : url("../images/icon/plus.svg");
			background-size  : contain;
			content          : "";
			height           : 5px;
			left             : 2px;
			position         : absolute;
			top              : 50%;
			transform        : translateY(-50%);
			width            : 5px;
			z-index          : 1;
		}
	}
}

.alert {
	background-color : #B66435;
	border-radius    : 31px;
	display          : flex;
	flex-direction   : column;
	margin-top       : 40px;
	overflow         : hidden;
	padding          : 29px 25px 0 25px;

	@media screen and (min-width : $break-table) {
		align-items     : flex-end;
		flex-direction  : row;
		justify-content : space-between;
		margin-top      : 60px;
		padding         : 30px 0 0 30px;
	}

	@media screen and (min-width : $break-desktop) {
		align-items : unset;
		padding     : 36px 30px 0 60px;
	}
}

.alert__left {

	padding-bottom : 30px;

	@media screen and (min-width : $break-table) {
		max-width      : 290px;
		padding-bottom : 35px;
		padding-right  : 36px;
	}

	@media screen and (min-width : 960px) {
		max-width : none;
	}

	@media screen and (min-width : $break-desktop) {
		max-width      : 500px;
		padding-bottom : 36px;
		padding-right  : 0;
	}
}

.alert__top {
	margin-bottom : 14px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 23px;
	}
}

.alert__subtitle {
	color         : $cream;
	font-size     : 12px;
	font-weight   : 400;
	line-height   : 15px;
	margin-bottom : 8px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 18px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 21px;
	}
}

.alert__title {
	color          : $cream;
	font-family    : 'Phosphate';
	font-size      : 16px;
	font-style     : normal;
	font-weight    : 400;
	line-height    : 24px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
		white-space : nowrap;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 34px;
	}
}

.alert__text {
	color       : $cream;
	font-size   : 12px;
	font-weight : 400;
	line-height : 18px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 18px;
		line-height : 24px;
	}
}

.alert__right {
	background-color        : $green;
	border-top-left-radius  : 31px;
	border-top-right-radius : 31px;
	padding                 : 25px 47px;

	@media screen and (min-width : $break-table) {
		min-width : 394px;
		padding   : 25px 29px 35px 25px;
	}

	@media screen and (min-width : $break-desktop) {
		min-width : 519px;
		padding   : 34px 48px;
	}
}

.alert__subtitle--green {
	font-size     : 12px;
	font-weight   : 400;
	line-height   : 18px;
	margin-bottom : 14px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 18px;
		line-height   : 24px;
		margin-bottom : 30px;
	}
}

.alert__wrap {

	display        : flex;
	flex-direction : column;
	margin-bottom  : 20px;

	@media screen and (min-width : $break-table) {
		align-items     : center;
		flex-direction  : row;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.alert__price {
	background-color : $cream;
	color            : $green;
	padding          : 18px 28px;
	text-align       : center;

	@media screen and (min-width : $break-table) {
		text-align : start;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 14px 38px;
	}

	& + & {
		margin-top : 16px;

		@media screen and (min-width : $break-table) {
			margin-top : 0;
		}
	}
}

.alert__name {
	color       : $dark-green;
	font-size   : 12px;
	font-weight : 400;
	line-height : 15px;


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 18px;
	}
}

.alert__sum {
	color       : $dark-green;
	font-size   : 30px;
	font-weight : 700;
	line-height : 38px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 36px;
		line-height : 46px;
	}
}


.alert__price--dark {
	background-color : $dark-green;

	.alert__name {
		color : $cream;
	}

	.alert__sum {
		color : $cream;
	}
}

.alert__bottom {
	color       : $cream;
	font-size   : 11px;
	font-weight : 400;
	line-height : 16px;


	@media screen and (min-width : $break-table) {
		text-align : right;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 12px;
		line-height : 24px;
	}
}

.housing-item__slider {
	height: 180px;
	@media screen and (min-width : $break-table) {
		height: 218px;
	}

	@media screen and (min-width : $break-desktop) {
		height: 225px;
	}
}
