.blog__list-title {
	margin-bottom : 40px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 60px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}
}

.blog-list__grid {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 40px;

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 24px;
		grid-row-gap          : 30px;
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 60px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 40px;

	}
}

.blog-card {
	background-color : $white;
	display          : flex;
	flex-direction   : column;
	grid-column      : 4 span;
	height           : 100%;
	margin           : 0 -16px;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
		margin      : 0;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 4 span;
	}
}

.blog-card__image {
	img {
		object-fit : cover;
		width      : 100%;
	}
}

.blog-card__main {
	display        : flex;
	flex-direction : column;
	height         : 100%;
	padding        : 20px 18px;

	@media screen and (min-width : $break-table) {
		padding : 22px 22px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.blog-card__name {
	color         : $dark;
	font-size     : 16px;
	font-weight   : 500;
	line-height   : 20px;
	margin-bottom : 12px;

	@media screen and (min-width : $break-table) {
		font-size     : 20px;
		line-height   : 25px;
		margin-bottom : 12px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.blog-card__about {
	color         : rgba(54, 45, 54, 0.7);
	font-size     : 12px;
	font-weight   : 400;
	line-height   : 15px;
	margin-bottom : 38px;

	@media screen and (min-width : $break-table) {
		font-size     : 14px;
		line-height   : 18px;
		margin-bottom : 42px;
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.blog-card__bottom {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin-top      : auto;
}

.blog-card__date {
	color        : rgba(54, 45, 54, 0.5);
	font-size    : 12px;
	font-weight  : 400;
	line-height  : 15px;
	padding-left : 20px;
	position     : relative;

	@media screen and (min-width : $break-table) {
		font-size    : 14px;
		line-height  : 18px;
		padding-left : 23px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:before {
		background-image  : url("../images/icon/calendar.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		height            : 13px;
		left              : 0;
		position          : absolute;
		top               : 50%;
		transform         : translateY(-50%);
		width             : 13px;
		z-index           : 1;

		@media screen and (min-width : $break-table) {
			height : 16px;
			width  : 16px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.blog-card__link {
	color         : $dark;
	font-size     : 16px;
	font-weight   : 400;
	line-height   : 21px;
	padding-right : 23px;
	position      : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	&:after {
		background-image  : url("../images/icon/arrow-right.svg");
		background-repeat : no-repeat;
		background-size   : cover;
		content           : "";
		height            : 16px;
		position          : absolute;
		right             : 0;
		top               : 50%;
		transform         : translateY(-50%);
		width             : 16px;
		z-index           : 1;
	}
}

.pagination {
	align-items     : center;
	display         : flex;
	justify-content : center;
}

.pagination__item {
	&:not(:first-child) {
		margin-left : 10px;
	}
}

.pagination__link {
	align-items      : center;
	background-color : transparent;
	color            : rgba(54, 45, 54, 0.5);
	display          : flex;
	font-size        : 14px;
	font-weight      : 400;
	height           : 40px;
	justify-content  : center;
	line-height      : 24px;
	transition       : color .3s ease;
	width            : 40px;

	&.active {
		background-color : $dark-brown;
		color            : $white;

		&:hover {
			color : $white;
		}
	}

	&:hover {
		color : $brown;
	}

	&--text {
		height : 24px;
		width  : 48px;
	}
}
