.big {
	background-color : $dark;
}

.big__title {
	margin-bottom : 21px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 30px;
		max-width     : 438px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
		max-width     : 438px;
	}
}

.big__grid {


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}
}

.big__wrap {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(3, 1fr);
	padding               : 0 23px;

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 24px;
		grid-row-gap          : 29px;
		grid-template-columns : repeat(6, 1fr);
		margin                : 0 auto;
		max-width             : 530px;
		padding               : 0;
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap  : 40px;
		margin    : 0;
		max-width : none;
		width     : 56%;
	}
}

.big__item {
	align-items      : center;
	background-color : $dark-brown;
	display          : flex;
	flex-direction   : column;
	grid-column      : 4 span;
	justify-content  : center;
	padding          : 40px 20px;
	text-align       : center;

	@media screen and (min-width : $break-table) {
		grid-column : 3 span;
		padding     : 50px 20px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.big__number {
	color          : $white;
	font-family    : 'Phosphate';
	font-size      : 48px;
	font-weight    : 400;
	line-height    : 60px;
	margin-bottom  : 2px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size     : 60px;
		line-height   : 56px;
		margin-bottom : 9px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 72px;
		line-height : 67px;
	}
}

.big__text {
	color       : $cream;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;


	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 32px;
	}

}
