  display: flex;
  position: relative;
}
.icons-group:not(:first-child) {
  padding-left: 14px;
}
.icons-group:not(:first-child):before {
  background-image: url("../images/icon/plus.svg");
  background-size: contain;
  content: "";
  height: 5px;
  left: 2px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 5px;
  z-index: 1;
}

