.friends {
	background-color : $dark;
}

.friends__grid {
	align-items           : center;
	display               : grid;
	grid-row-gap          : 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 20px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.friends__info {
	grid-column : 3 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.friends__price {
	grid-column : 3 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.friends__title {
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 30px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.friends__text {
	color       : $white;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 38px;
	}
}
