.blog__page-title {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}
}

.blog {
	margin-bottom : 40px;
}

.blog__head {
	margin-bottom : 30px;
}

.blog__title {
	color         : $dark;
	font-size     : 22px;
	font-weight   : 500;
	line-height   : 28px;
	margin-bottom : 14px;

	@media screen and (min-width : $break-table) {
		font-size     : 28px;
		line-height   : 35px;
		margin-bottom : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 36px;
		line-height : 45px;
	}
}

.blog__date {
	color       : rgba(54, 45, 54, 0.5);
	font-size   : 12px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 20px;
	}

	span {
		&:last-child {
			padding-left : 10px;
		}
	}
}

.blog__poster {
	margin        : 0 -16px 24px -16px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin : 0 0 30px 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 40px;
	}
}

.blog__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 32px;
	}

	&:not(:last-child) {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 30px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 40px;
		}
	}

	p {
		&:not(:last-child) {
			margin-bottom : 24px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 30px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 40px;
			}
		}
	}
}

.blog__subtitle {
	color         : $dark;
	font-size     : 18px;
	font-weight   : 500;
	line-height   : 24px;
	margin-bottom : 16px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 26px;
		line-height   : 38px;
		margin-bottom : 24px;
	}
}

.blog__image {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}

	.images-about {
		color       : rgba(54, 45, 54, 0.5);
		font-size   : 12px;
		font-weight : 400;
		line-height : 22px;
		text-align  : center;

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 22px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 30px;
		}
	}
}

.blog__images {
	display               : grid;
	grid-gap              : 12px;
	grid-template-columns : repeat(7, 1fr);
	margin-bottom         : 14px;

	@media screen and (min-width : $break-table) {
		grid-gap              : 30px;
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap      : 40px;
		margin-bottom : 24px;
	}

	picture {
		&:nth-of-type(1) {
			grid-column : 1/5;

			@media screen and (min-width : $break-table) {
				grid-column : 1/8;
			}

			@media screen and (min-width : $break-desktop) {
				grid-column : 2/7;
			}
		}

		&:nth-of-type(2) {
			grid-column : 5/8;

			@media screen and (min-width : $break-table) {
				grid-column : 8/13;
			}

			@media screen and (min-width : $break-desktop) {
				grid-column : 7/12;
			}
		}
	}
}


.blog__wrap {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 30px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 40px;
	}


	.blog__subtitle {
		margin-bottom : 0;
	}

	.swiper-nav {
		@media screen and (min-width : $break-desktop) {
			display : none;
		}
	}
}

.swiper--blog {
	margin : 0 -24px;

	@media screen and (min-width : $break-table) {
		margin : 0 -12px;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 -20px;
	}

	.swiper-slide {
		width : 100%;

		@media screen and (min-width : $break-table) {
			width : 50%;
		}

		@media screen and (min-width : $break-desktop) {
			width : 33%;
		}

		.blog-card {
			margin : 0 8px;

			.blog-card__main {


				@media screen and (min-width : $break-table) {
					padding : 20px 22px;
				}

				@media screen and (min-width : $break-desktop) {

				}
			}

			@media screen and (min-width : $break-table) {
				margin : 0 12px;
			}

			@media screen and (min-width : $break-desktop) {
				margin : 0 20px;
			}
		}
	}
}
