.lab {
	background-color : $green;
}

.lab__title {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}
}

.lab__wrapper {

	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.lab__group {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		margin-right  : 40px;
		max-width     : 328px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 500px;
	}
}

.lab__subtitle {
	margin-bottom : 18px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.lab__text {
	color       : $white;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;


	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 38px;
	}
}

.lab__price {
	@media screen and (min-width : $break-table) {

		max-width : 560px;
	}
}

.price {
	background-color : $dark-green;
	margin           : 0 -16px;
	padding          : 30px 16px;

	@media screen and (min-width : $break-table) {
		margin : 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 50px 30px;
	}
}

.price__group {
	align-items           : center;
	color                 : $cream;
	display               : grid;
	grid-template-columns : repeat(3, 1fr);


	&:not(:last-child) {
		margin-bottom : 12px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 20px;
		}
	}

	&--end {
		align-items : end;
	}
}

.price__row {
	border-bottom  : 1px solid $cream;
	padding-bottom : 12px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 20px;
	}

	&:not(:first-child) {
		padding-top : 12px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			padding-bottom : 20px;
		}
	}
}

.price__sum {
	font-size   : 15px;
	font-weight : 400;
	line-height : 19px;
	text-align  : right;
	white-space : nowrap;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 21px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 26px;
		line-height : 34px;
	}

	&--center {
		text-align : center;
	}
}

.price__name {
	font-size   : 12px;
	font-weight : 400;
	line-height : 18px;

	max-width   : 250px;

	@media screen and (min-width : $break-table) {
		font-size   : 13px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 32px;
	}

}

.price__wrap {
	min-width : 145px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		min-width : 250px;
	}
}

.price__subname {

	font-size   : 10px;
	font-weight : 400;
	line-height : 13px;
	margin-top  : 5px;


	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 22px;
		max-width   : 270px;
	}

}

.price__text {

	font-size   : 10px;
	font-weight : 400;
	line-height : 13px;
	text-align  : right;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 21px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 26px;
		line-height : 34px;
	}

}

.price__count {
	font-size   : 14px;
	font-weight : 400;
	line-height : 22px;
}
