// Arrow
.swiper-button-prev,
.swiper-button-next {
	cursor                      : pointer;
	display                     : inline-block;
	flex                        : 0 0 46px;
	height                      : 20px;
	outline                     : 0;
	position                    : relative;
	-webkit-tap-highlight-color : transparent;
	transition                  : opacity .3s ease;
	user-select                 : none;
	vertical-align              : top;
	width                       : 46px;

	&:after {
		content  : '';
		left     : 0;
		position : absolute;
		top      : 0;
	}

	@media screen and (min-width : 1076px) {
		&:hover {
			opacity : .7;
		}
	}

	&.swiper-button-disabled {
		cursor         : auto;
		opacity        : 0.3;
		pointer-events : none;
	}
}

.swiper-button-prev {
	margin-right : 25px;

}

.swiper-button-next {
	margin-left : 25px;
}

.slider__pagination {
	display         : flex;
	flex-flow       : row nowrap;
	justify-content : space-between;
}

.swiper-wrap {
	margin-bottom : 20px;
}

.swiper-nav {
	display : flex;
}

.swiper-button {
	cursor : pointer;
	height : 34px;
	width  : 34px;

	& + & {
		margin-left : 6px;
	}

	@media screen and (min-width : $break-table) {
		height : 40px;
		width  : 40px;
	}

	@media screen and (min-width : 1200px) {

	}
}

.swiper-counter {
	color       : #362D3680;
	font-size   : 16px;
	font-weight : 500;
	line-height : 24px;

	.current-slide {
		color       : #362D36;
		font-weight : 500;
	}

	.total-slides {


	}
}

.swiper-wrap {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
}
