.activity {
	margin-bottom : -220px;
	position      : relative;
	z-index       : 10;

	@media screen and (min-width : $break-table) {
		margin-bottom : -237px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : -293px;
	}

	.b-btn--brown {
		left      : 50%;
		position  : relative;
		transform : translateX(-50%);
	}
}

.activity__title {
	color          : #85331F;
	font-family    : 'Phosphate';
	font-size      : 16px;
	font-style     : normal;
	font-weight    : 400;
	line-height    : 24px;
	margin-bottom  : 30px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size     : 20px;
		line-height   : 28px;
		margin-bottom : 40px;
		text-align    : center;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 28px;
		line-height   : 34px;
		margin-bottom : 60px;
	}
}

.activity__price {
	background-color      : $dark-green;
	display               : grid;
	grid-template-columns : repeat(4, 1fr);
	margin-bottom         : 40px;

	@media screen and (min-width : $break-table) {
		align-items           : baseline;
		grid-column-gap       : 40px;
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 60px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap : 60px;
		margin-bottom   : 80px;
	}
}


.price--activity {
	grid-column : 6 span;

	.price__name {
		@media screen and (min-width : $break-table) {
			min-width : 233px;
		}
	}

	&:nth-of-type(1) {
		padding-bottom : 0;

		@media screen and (min-width : $break-table) {
			padding-bottom : 30px;
		}

		@media screen and (min-width : $break-desktop) {
			padding-bottom : 50px;
		}
	}
}
