.individual {
	background-color : $dark;
}

.individual__grid {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 24px;
		grid-row-gap          : 30px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 40px;
	}
}

.individual__title {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
		grid-row    : 1/3;
	}

	@media screen and (min-width : 1550px) {

	}
}

.travel-box {
	&--individual-one {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 1/7;
			grid-row    : 3/6;
		}

		@media screen and (min-width : 1550px) {

		}
	}

	&--individual-two {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 7/13;
			grid-row    : 2/6;
		}

		@media screen and (min-width : 1550px) {

		}
	}

	&--individual-three {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 1/7;
		}

		@media screen and (min-width : 1550px) {

		}
	}

	&--individual-four {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 7/13;
		}

		@media screen and (min-width : 1550px) {

		}
	}
}
