.history {
	background-color : $dark;
}

.history__wrap {


	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.history__wrap--bottom {
	display        : flex;
	flex-direction : column-reverse;

	@media screen and (min-width : $break-table) {
		flex-direction : row;
	}
}

.history__group {
	&:not(:last-child) {
		margin-bottom : 51px;
	}
}

.history__group--bottom {


	.history__title {

		@media screen and (min-width : $break-table) {
			text-align : right;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.history__title {
	color         : $white;
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}

}

.history__image {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}

}

.history__text {
	color       : $white;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 18px;
		line-height : 26px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		line-height : 32px;
	}
}

.history__text--top {

	@media screen and (min-width : $break-table) {
		margin-left : 40px;
		max-width   : 348px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 97px;
		max-width   : 508px;
	}
}

.history__text--bottom {


	@media screen and (min-width : $break-table) {
		margin-right : 40px;
		max-width    : 313px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-right : 97px;
		max-width    : 418px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 30px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 60px;
			}

			@media screen and (min-width : $break-desktop) {

			}

		}
	}

	span {
		color       : $cream;
		display     : block;
		font-size   : 14px;
		font-weight : 400;
		line-height : 20px;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
			max-width   : 279px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 18px;
			line-height : 24px;
			max-width   : 306px;
		}
	}
}
