.gallery {
	padding : 100px 0 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 116px 0 60px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 148px 0 80px 0;
	}
}

.gallery__title {
	margin-bottom : 60px;
}

.gallery__gird {
	display               : grid;
	grid-gap              : 7px;
	grid-template-columns : repeat(8, 1fr);
	margin-bottom         : 30px;

	@media screen and (min-width : $break-table) {
		grid-gap              : 20px;
		grid-template-columns : repeat(12, 1fr);
		margin-bottom         : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}
}

.gallery__image {


	&--one {
		grid-column : 1/10;

		@media screen and (min-width : $break-table) {
			grid-column : 1/9;
			grid-row    : 1/4;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&--two {
		grid-column : 1/4;
		grid-row    : 2/2;

		@media screen and (min-width : $break-table) {
			grid-column : 9/13;
			grid-row    : 1/3;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&--three {
		grid-column : 4/10;
		grid-row    : 2/2;

		@media screen and (min-width : $break-table) {
			grid-column : 9/13;
			grid-row    : 3/4;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&--four {
		grid-column : 1/6;

		@media screen and (min-width : $break-table) {
			grid-column : 1/6;
			grid-row    : 4/5;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&--five {
		grid-column : 6/10;

		@media screen and (min-width : $break-table) {
			grid-column : 6/13;
			grid-row    : 4/5;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&--six {
		grid-column : 1/10;

		@media screen and (min-width : $break-table) {
			grid-column : 1/5;
			grid-row    : 5/5;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&--seven {
		grid-column : 1/5;


		@media screen and (min-width : $break-table) {
			grid-column : 5/9;
			grid-row    : 5/5;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&--eight {
		grid-column : 5/9;

		@media screen and (min-width : $break-table) {
			grid-column : 9/13;
			grid-row    : 5/5;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.b-btn--gallery {
	left      : 50%;
	position  : relative;
	transform : translateX(-50%);
}
