.newbie-about {

}

.newbie-about__wrapper {

}

.newbie-about__top {
	align-items           : center;

	display               : grid;
	grid-template-columns : repeat(3, 1fr);
	margin-bottom         : 40px;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(14, 1fr);
		margin-bottom         : 80px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-about__img {
	display       : block;
	grid-column   : 3 span;
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		grid-column   : 8 span;
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-about__wrap {
	grid-column : 3 span;

	@media screen and (min-width : $break-table) {
		grid-column : 10/15;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 11/15;
	}
}

.newbie-about__title {
	margin-bottom : 16px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.newbie-about__text {
	color       : $dark;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 38px;
	}
}

.newbie-about__text--small {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;
	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 32px;
	}
}

.newbie-about__bottom {
	align-items           : center;
	display               : grid;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(14, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}

	.newbie-about__title {
		grid-column : 3 span;

		@media screen and (min-width : $break-table) {
			grid-column   : 6 span;
			margin-bottom : 0;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	.newbie-about__text {
		grid-column : 3 span;

		@media screen and (min-width : $break-table) {
			grid-column : 10/15;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 11/15;
		}
	}
}
