.people {
	background-color : $dark;
}

.people__group {
	display        : flex;
	flex-direction : column-reverse;

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		flex-direction  : row;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&:not(:last-child) {
		margin-bottom : 40px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 60px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 80px;
		}
	}
}

.people__left {


	@media screen and (min-width : $break-table) {
		max-width : 350px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.people__about {
	color         : $white;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 22px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 20px;
		line-height   : 32px;
		margin-bottom : 35px;
	}
}

.people__name {
	color       : $white;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 38px;
	}

	&:not(:last-child) {
		margin-bottom : 18px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 35px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

}

.people__img {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		max-width     : 319px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : none;
	}
}
