.guides {
	background-color : $green;
	padding          : 280px 0 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 312px 0 140px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 400px 0 262px 0;
	}
}


.guides__grid {
	display               : grid;
	grid-gap              : 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 40px;
	}
}

.guides__text {
	color       : $white;
	font-size   : 16px;
	font-weight : 400;
	grid-column : 1/4;
	line-height : 24px;


	@media screen and (min-width : $break-table) {
		font-size     : 20px;
		grid-column   : 1/13;
		line-height   : 28px;
		margin-bottom : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 28px;
		line-height   : 38px;
		margin-bottom : 20px;
	}
}

.guides__img--top {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
		grid-row    : 2/4;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.price--accompanying {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 7/13;
		grid-row    : 2/5;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.guides__img {
	margin : 0 -16px;

	@media screen and (min-width : $break-table) {
		margin : 0;
	}
}

.price--individual {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
		grid-row    : 4/8;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.guides__img--bottom {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 7/13;
		grid-row    : 5/8;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.price--guides {
	background-color : $dark-brown;

	.price__name {
		color       : #E2E2E2;
		font-size   : 12px;
		font-weight : 400;
		line-height : 15px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 17px;
			line-height : 24px;
		}
	}
}

.price__title {
	color          : #FFF;
	font-family    : 'Phosphate';
	font-size      : 16px;
	font-weight    : 400;
	line-height    : 24px;
	margin-bottom  : 21px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 18px;
		line-height : 20px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 28px;
		line-height   : 34px;
		margin-bottom : 34px;
	}
}
