.footer {
	flex    : 0 0 auto;
	padding : 26px 0 20px 0;
	
	@media screen and (min-width : $break-table) {
		padding : 20px 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.footer__wrapper {
	align-items           : center;
	display               : grid;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.footer__logo {
	grid-column   : 1/2;
	grid-row      : 1/1;
	margin-bottom : 17px;

	@media screen and (min-width : $break-table) {
		grid-column   : 1/2;
		grid-row      : unset;
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/3;
	}
}

.footer-contacts {
	grid-column : 4/5;
	grid-row    : 1/3;

	@media screen and (min-width : $break-table) {
		align-items : center;
		display     : flex;
		grid-column : 3/9;
		grid-row    : unset;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 3/8;
	}
}

.footer-contacts__item {
	&:not(:last-child) {
		margin-bottom : 18px;
		@media screen and (min-width : $break-table) {
			margin-bottom : 0;
		}
	}

	&:not(:first-child) {

		@media screen and (min-width : $break-table) {
			margin-left : 22px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-left : 48px;
		}
	}

	a {
		color       : $dark;
		font-size   : 12px;
		font-weight : 400;
		line-height : 16px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 14px;
			line-height : 24px;
		}
	}
}

.footer-contacts__name {
	color          : $green;
	font-size      : 11px;
	font-weight    : 400;
	line-height    : 10px;
	margin-bottom  : 8px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}
}

.footer__wrap {
	grid-column : 1/3;
	grid-row    : 2/3;


	@media screen and (min-width : $break-table) {
		align-items    : flex-start;
		display        : flex;
		flex-direction : column;
		grid-column    : 10/13;
		grid-row       : unset;
	}

	@media screen and (min-width : $break-desktop) {
		align-items     : center;
		flex-direction  : row;
		grid-column     : 8/13;
		justify-content : flex-end;
	}
}

.footer__politic {
	color       : $dark;
	font-size   : 12px;
	font-weight : 400;
	line-height : 16px;
	position    : relative;


	@media screen and (min-width : $break-table) {
		margin-bottom : 8px;
		&:before {
			background-color : $dark;
			bottom           : 0;
			content          : "";
			height           : 1px;
			left             : 0;
			position         : absolute;
			width            : 100%;
			z-index          : 1;
		}
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 14px;
		line-height   : 24px;
		margin-bottom : 0;
	}
}

.footer__design {
	color       : $dark;
	font-size   : 12px;
	font-weight : 400;
	line-height : 16px;
	margin-top  : 19px;

	@media screen and (min-width : $break-table) {
		margin-top : 0;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 24px;
		margin-left : 33px;
	}
}
