.care__title {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : 1080px) {
		margin-bottom : 60px;
	}
}

.care__grid {
	display               : grid;
	grid-row-gap          : 24px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-row-gap          : 20px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : 1080px) {
		grid-column-gap : 90px;
		grid-row-gap    : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap : 100px;
	}
}

.care__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	grid-column : 1/4;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		grid-column : 1/6;
		line-height : 24px;
	}

	@media screen and (min-width : 1080px) {
		font-size   : 20px;
		grid-column : 1/7;
		line-height : 32px;
	}

	&:nth-of-type(5) {

		@media screen and (min-width : $break-table) {
			display         : flex;
			flex-direction  : column;
			grid-column     : 7/13;
			justify-content : end;
		}

		@media screen and (min-width : 1080px) {
			display     : block;
			grid-column : 1/7;
		}
	}
}

.care__img {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 7/13;
		grid-row    : 1/4;
	}

	@media screen and (min-width : 1080px) {
		grid-row : 1/6;
	}

	img {
		height : 100%;
	}
}
