.hero {
	background-color  : $dark-green;
	background-repeat : no-repeat;
	background-size   : cover;
	display           : flex;
	flex-direction    : column;
	min-height            : 100vh;
	justify-content   : flex-end;
	padding-bottom    : 40px;
	position: relative;
	@media screen and (min-width : $break-table) {
		padding-bottom : 60px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 78px;
	}
}

.hero__wrapper {
	position: relative;
	z-index: 11;
	@media screen and (min-width : $break-table) {
		align-items     : flex-end;
		display         : flex;
		justify-content : space-between;
	}
}

.hero__title {
	color         : $white;
	font-family   : 'Phosphate';
	font-size     : 36px;
	font-weight   : 400;
	line-height   : 48px;
	margin-bottom : 10px;


	@media screen and (min-width : $break-table) {
		font-size     : 54px;
		line-height   : 69px;
		margin-bottom : 0;
		max-width     : 428px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 87px;
		line-height   : 104px;
		margin-bottom : 0;
		max-width     : 678px;
	}
}

.hero__right {
	max-width : 320px;

	@media screen and (min-width : $break-table) {
		margin-left : 52px;
	}

	.b-btn--brown {
		min-width : 252px;
	}
}

.hero__text {
	color         : $white;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 24px;
	margin-bottom : 24px;

	@media screen and (min-width : $break-desktop) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 32px;
	}

	@media screen and (min-width : $break-table) {

	}
}

.hero--base {
	background-position : center;
}

.hero__wrapper--column {
	align-items    : flex-start;
	flex-direction : column;
}

.hero__subtext {
	color       : $cream;
	font-size   : 16px;
	font-weight : 400;
	line-height : 20px;
	margin-top  : 16px;


	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 38px;
	}
}

.hero__subtext--base {
	max-width : 640px;
}

.hero--healthy {
	background-position : right -252px bottom;

	@media screen and (min-width : $break-table) {
		background-position : center bottom;
	}
}

.hero__subtext--healthy {
	max-width : 798px;
}

.hero--housing {
	background-position : right -252px center;

	@media screen and (min-width : $break-table) {
		background-position : center;
	}
}

.hero__title--housing {
	max-width : 800px;
}

.hero__subtext--housing {
	max-width : 753px;
}


.hero--food {
	background-position : right -252px bottom;

	@media screen and (min-width : $break-table) {
		background-position : center bottom;
	}
}

.hero__title--food {
	max-width : 1000px;
}

.hero--carsharing {
	background-position : right -252px bottom;

	@media screen and (min-width : $break-table) {
		background-position : center bottom;
	}
}

.hero__title--carsharing {
	max-width : 1200px;
}

.hero__subtext--carsharing {
	max-width : 750px;
}


.hero--companies {
	background-position : right -252px bottom;

	@media screen and (min-width : $break-table) {
		background-position : center bottom;
	}
}

.hero__title--companies {
	max-width : 1200px;
}

.hero__subtext--companies {
	max-width : 832px;
}


.hero--children {
	background-position : right -252px bottom;

	@media screen and (min-width : $break-table) {
		background-position : center bottom;
	}
}


.hero--travel {
	background-position : right -252px bottom;

	@media screen and (min-width : $break-table) {
		background-position : center bottom;
	}
}

.hero__title--travel {
	max-width : none;
}

.hero__subtext--travel {
	max-width : 840px;
}

.video {
	position: absolute;
	height: 100%;
	width: 100%;
	z-index: 10;
	left: 0;
	top: 0;
	right: 0;
	bottom: 0;
	object-fit: cover;
}
