.newbie-base__title {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}
}

.newbie-base__wrapper {

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-base__wrap {
	margin-bottom : 40px;
	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
	}

	.b-btn--brown {
		width : 100%;

		@media screen and (min-width : $break-table) {
			width : auto;
		}
	}
}

.newbie-base__img {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}

	img {
		object-fit : cover;
		width      : 100%;
	}
}

.newbie-base__accordion {


	@media screen and (min-width : $break-table) {
		padding-left : 50px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width    : 630px;
		padding-left : 70px;
	}
}

.newbie-base__slider {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 60px;
		max-width     : 531px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
