.holidays__title {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}
}

.holidays__grid {
	display               : grid;
	grid-gap              : 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 23px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 40px;
	}
}

.holidays__img {
	grid-column : 3 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.holidays__wrap {
	background-color : $dark-brown;
	grid-column      : 3 span;
	margin           : 0 -16px;
	padding          : 32px 16px;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
		margin      : 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 50px 30px;
	}
}

.holidays__top {
	margin-bottom : 24px;
	@media screen and (min-width : $break-table) {
		font-size   : 13px;
		line-height : 15px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 24px;
	}
	@media screen and (min-width : $break-table) {
		font-size   : 13px;
		line-height : 15px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 20px;
		line-height   : 24px;
		margin-bottom : 40px;
	}
}

.holidays__list {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 40px;
	}

	li {
		border-bottom  : 1px solid $cream;
		color          : $cream;
		font-size      : 15px;
		font-weight    : 400;
		line-height    : 19px;
		padding-bottom : 12px;
		text-align     : center;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 21px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size      : 26px;
			line-height    : 34px;
			padding-bottom : 20px;
		}

		&:not(:first-child) {
			padding-top : 12px;

			@media screen and (min-width : $break-desktop) {
				padding-top : 20px;
			}
		}
	}
}

.holidays__bottom {
	color       : $white;
	font-size   : 12px;
	font-weight : 400;
	line-height : 15px;

	@media screen and (min-width : $break-table) {
		font-size   : 13px;
		line-height : 15px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 24px;
	}

	p {

		&:not(:last-child) {
			margin-bottom : 15px;

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 24px;
			}
		}
	}
}
