.accordion-mini {
	&:not(:last-child) {
		margin-bottom : 8px;
	}
}

.accordion-mini__content {
	background-color : $white;
	box-sizing       : content-box;
	color            : $dark;
	display          : none;
	font-size        : 12px;
	font-weight      : 400;
	//max-height       : 0;
	//opacity          : 0;
	//overflow         : hidden;
	line-height      : 16px;
	padding          : 0 16px;
	transition       : all 0.3s ease-out;
	will-change      : max-height;


	@media screen and (min-width : $break-table) {


	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 25px;
		padding     : 0 24px;
	}
}

.accordion-mini__control {
	align-items      : center;
	background-color : $white;
	border           : none;
	border-radius    : 2.65151px;
	color            : #000;
	cursor           : pointer;
	display          : flex;
	font-size        : 16px;
	font-weight      : 400;
	justify-content  : space-between;
	letter-spacing   : 0.3px;
	line-height      : 21px;
	padding          : 16px 16px;
	text-align       : left;
	transition       : transform .3s ease, background-color .3s ease;
	width            : 100%;

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		line-height : 31px;
		padding     : 24px 24px;
	}

}

.accordion-mini__icon {
	transition : transform .3s ease;
}

.accordion-mini.open .accordion-mini__content {
	display : block;
	opacity : 1;
	padding : 0 16px 16px 16px;

	@media screen and (min-width : $break-desktop) {
		padding : 0 24px 24px 24px;
	}
}

.accordion-mini.open .accordion-mini__control {
	border-bottom : transparent;
}

.accordion-mini.open .accordion-mini__content {

}

.accordion-mini.open .accordion-mini__icon {
	transform : rotate(224deg);
}
