.movement {
	background-color : $dark;
}

.movement__title {
	margin-bottom : 30px;
	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}
}


.movement__wrapper {

	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}
}


.movement__about {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		margin-right  : 57px;
		max-width     : 365px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-right : 116px;
		max-width    : 525px;
	}
}

.movement__subtitle {
	margin-bottom : 18px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}
}

.movement__text {
	color       : $white;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;


	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 38px;
	}
}
