.feedback {
	background-position : bottom;
	background-repeat   : no-repeat;
	background-size     : cover;
	padding             : 40px 0 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 60px 0 60px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 100px 0 156px 0;
	}
}


.feedback__wrapper {


	@media screen and (min-width : $break-table) {
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {
	}
}

.feedback__main {
	margin-bottom : 50px;


	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		max-width     : 322px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 460px;
	}
}

.feedback__title {
	color         : $white;
	font-size     : 22px;
	font-weight   : 400;
	line-height   : 30px;
	margin-bottom : 18px;

	@media screen and (min-width : $break-table) {
		font-size   : 26px;
		line-height : 36px;
		max-width   : 300px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 36px;
		line-height   : 40px;
		margin-bottom : 24px;
		max-width     : 431px;
	}
}

.feedback__subtitle {
	max-width : 290px;

	p {
		color       : $white;
		font-size   : 14px;
		font-weight : 400;
		line-height : 20px;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 18px;
			line-height : 24px;
		}
	}
}

.feedback__form {


	@media screen and (min-width : $break-table) {
		min-width : 299px;
	}

	@media screen and (min-width : $break-desktop) {
		min-width : 440px;
	}
}
