.newbie-housing {
	background-color : $green;

	img {
		max-width  : none;
		object-fit : cover;
		width      : 100%;
	}
}

.newbie-housing__title {

}

.newbie-housing__grid {
	align-items           : center;
	display               : grid;
	grid-row-gap          : 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 44px;
		grid-row-gap          : 0px;
		grid-template-columns : repeat(12, 1fr);
		grid-template-rows    : repeat(12, 50px);
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap : 72px;
		grid-row-gap    : 10px;
	}
}


.newbie-housing__text {
	color       : $cream;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 32px;
	}
}

.newbie-housing__text--first {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/5;
		grid-row    : 1/3;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-housing__sm {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/5;
		grid-row    : 7/4;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-housing__text--two {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/5;
		grid-row    : 7/13;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-housing__title {
	grid-column : 1/4;
	grid-row    : 1/1;

	@media screen and (min-width : $break-table) {
		grid-column : 5/9;
		grid-row    : 1/2;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-housing__big {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 5/9;
		grid-row    : 3/8;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-housing__text--three {
	grid-column : 1/4;


	@media screen and (min-width : $break-table) {
		grid-column : 5/9;
		grid-row    : 9/13;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-housing__text--four {
	color       : $white;
	font-size   : 16px;
	grid-column : 1/4;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 38px;
	}


	@media screen and (min-width : $break-table) {
		grid-column : 9/13;
		grid-row    : 3/5;
	}

	@media screen and (min-width : $break-desktop) {

	}
}


.newbie-housing__md {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 9/13;
		grid-row    : 9/11;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
