
.overlay {
	//opacity: 0;
	background : rgba(11, 12, 10, 0.47);
	bottom     : 0;
	left       : 0;
	opacity    : 0;
	position   : fixed;
	right      : 0;
	//z-index: -1;
	top        : 0;
	transition : opacity 0.5s ease, visibility 0.3s ease;
	visibility : hidden;
	z-index    : 1000;

	&.active {
		opacity    : 1;
		transition : opacity 0.5s ease, visibility 0.3s ease;
		visibility : visible;
	}
}

.popup {
	background-color : $cream;
	border-radius    : 26px;
	left             : 50%;
	max-width        : 320px;
	//z-index: -3;
	opacity          : 0;
	padding          : 0 15px;
	position         : fixed;
	top              : 50%;
	//opacity: 0;
	transform        : translate(-50%, -45%);
	transition       : opacity 0.5s ease, visibility 0.3s ease;
	visibility       : hidden;
	width            : 100%;

	&.active {
		opacity    : 1;
		transition : opacity 0.5s ease, visibility 0.3s ease;
		visibility : visible;
		z-index    : 1001;
	}

	&__wrap {
		align-items     : center;
		display         : flex;
		flex-direction  : column;
		justify-content : center;
		min-height      : 220px;
		padding         : 30px;
		position        : relative;
	}

	&__close {
		background-color : transparent;
		border           : none;
		position         : absolute;
		right            : -32px;
		top              : -11px;
		z-index          : 999;
	}
}

.popup--questions {
	background-color : $cream;
	max-width        : 260px;
	padding          : 0;
	width            : 100%;

	@media screen and (min-width : $break-table) {
		max-width : 400px;
	}

	@media screen and (min-width : $break-desktop) {
		max-width : 500px;
	}

	.popup__wrap {
		align-items     : center;
		display         : flex;
		flex-direction  : column;
		justify-content : center;
		min-height      : 220px;
		padding         : 40px 20px;
		position        : relative;

		@media screen and (min-width : $break-table) {
			padding : 48px 34px;
		}

		@media screen and (min-width : $break-desktop) {
			padding : 48px 49px;
		}

	}
}

.popup__title {
	color          : $brown;
	font-family    : 'Phosphate';
	font-size      : 24px;
	font-style     : normal;
	font-weight    : 400;
	line-height    : 32px;
	margin-bottom  : 24px;
	text-align     : center;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 26px;
		line-height : 40px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&--green {
		color : $green;
	}
}

.popup--succsess {
	max-width : 254px;
	padding   : 40px 16px;

	@media screen and (min-width : $break-table) {
		max-width : 400px;
		padding   : 49px 49px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.popup__wrap {
		padding : 0;
	}

	.popup__img {
		margin-bottom : 24px;
	}
}

.popup__text {
	color       : #362D36;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;
	text-align  : center;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}
}

.popup__form {
	width : 100%;
}
