.festival {

}

.festival__title {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 80px;
	}
}

.festival__column {
	display        : flex;
	flex-direction : column;
	gap            : 40px;
	grid-column    : 6 span;
}


.festival__grid {
	display               : grid;
	grid-gap              : 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		align-items : end;
		grid-gap    : 40px;
	}
}

.price--festival {
	background-color : $dark-brown;
	grid-column      : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
		grid-row    : 1/2;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.price__name {
		font-size   : 12px;
		font-weight : 500;
		line-height : 14px;
		max-width   : 187px;

		@media screen and (min-width : $break-table) {
			font-size   : 13px;
			line-height : 15px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 20px;
			line-height : 24px;
		}
	}

	.price__group {
		align-items : flex-end;
	}
}

.price__start {
	color       : $cream;
	font-size   : 10px;
	font-weight : 400;
	line-height : 13px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 14px;
		line-height : 22px;
	}
}

.price__date {
	color       : $cream;
	font-size   : 15px;
	font-weight : 400;
	line-height : 19px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 26px;
		line-height : 34px;
	}
}

.price__wrapper {

	&:not(:last-child) {
		margin-bottom : 12px;
	}
}


.price__column {
	min-width : 77px;

	@media screen and (min-width : $break-table) {
		min-width : 133px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.festival__img--first {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
		grid-row    : 1/3;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.festival__img--second {
	display     : flex;
	grid-column : 1/4;
	height      : 100%;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
		grid-row    : 2/4;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.festival__program {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 7/13;
		grid-row    : 3/5;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.festival__subtitle {
	color          : $brown;
	font-family    : 'Phosphate';
	font-size      : 16px;
	font-style     : normal;
	font-weight    : 400;
	line-height    : 24px;
	margin-bottom  : 30px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 28px;
		line-height   : 34px;
		margin-bottom : 60px;
	}
}

.festival__list {
	li {
		border-bottom  : 1px solid $dark;
		color          : $dark;

		font-size      : 12px;
		font-weight    : 400;
		line-height    : 18px;
		padding-bottom : 14px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size      : 20px;
			line-height    : 24px;
			padding-bottom : 20px;
		}

		&:not(:first-child) {
			padding-top : 14px;

			@media screen and (min-width : $break-desktop) {
				padding-top : 20px;
			}
		}
	}
}

.price--housing {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.price__table {
	color          : $cream;
	padding-bottom : 20px;
	text-align     : right;

	thead {
		font-size   : 10px;
		line-height : 13px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 16px;
			line-height : 21px;
		}
	}

	tbody {

		td {
			border-bottom  : 1px solid $cream;
			max-width      : 153px;
			padding-bottom : 12px;

			@media screen and (min-width : $break-table) {
				padding-bottom : 20px;
			}

			&:first-child {
				font-size      : 12px;
				font-weight    : 400;
				line-height    : 15px;
				text-align     : left;
				vertical-align : bottom;

				@media screen and (min-width : $break-table) {
					font-size   : 13px;
					line-height : 15px;
				}

				@media screen and (min-width : $break-desktop) {
					font-size   : 20px;
					line-height : 24px;
				}
			}
		}
	}


	.price__text {
		color       : $cream;
		font-size   : 10px;
		line-height : 13px;
		margin-left : auto;
		margin-top  : 10px;
		max-width   : max-content;
		text-align  : center;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 21px;
		}

		@media screen and (min-width : $break-desktop) {
			max-width : 100px;
		}
	}

	.price__sum {
		margin-top : 20px;

		@media screen and (min-width : $break-table) {
			margin-top : 40px;
		}
	}
}


.price__alert {
	color       : $white;
	font-size   : 12px;
	font-weight : 400;
	line-height : 15px;
	padding-top : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 13px;
		line-height : 15px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 24px;
		padding-top : 40px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 14px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 24px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}
}
