.cases__grid {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 24px;
		grid-row-gap          : 30px;
		grid-template-columns : repeat(8, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap              : 40px;
		grid-template-columns : repeat(12, 1fr);
	}
}

.cases__img {
	height : 180px;

	@media screen and (min-width : $break-table) {
		height : 226px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.cases__item {
	background-color : rgba(255, 255, 255, 0.5);
	display          : flex;
	flex-direction   : column;
	grid-column      : 4 span;
}

.cases__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 22px;
	padding     : 13px 30px;
	text-align  : center;

	@media screen and (min-width : $break-table) {
		font-size   : 14px;
		line-height : 22px;
		padding     : 20px 30px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 24px;
	}
}
