.newbie-tour {
}

.newbie-tour__grid {
	display               : grid;
	grid-row-gap          : 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 40px;
		grid-row-gap          : 40px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap : 65px;
		grid-row-gap    : 60px;

	}
}

.newbie-tour__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 32px;
	}
}

.newbie-tour__title {
	grid-column : 1/4;

	span {
		display   : block;
		max-width : 537px;
	}

	@media screen and (min-width : $break-table) {
		grid-column : 6/13;
		grid-row    : 1/2;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-tour__text--one {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {

		grid-column : 1/6;
		grid-row    : 5/7;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/6;
		grid-row    : 4/7;
	}
}

.newbie-tour__first {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/6;
		grid-row    : 1/5;
	}

	@media screen and (min-width : $break-desktop) {
		grid-row : 1/4;
	}
}

.newbie-tour__text--two {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 6/12;
		grid-row    : 2/3;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-tour__two {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 6/9;
		grid-row    : 3/5;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-tour__three {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 9/13;
		grid-row    : 4/7;
	}

	@media screen and (min-width : $break-desktop) {
		grid-row : 4/7;
	}
}

.newbie-tour__text--three {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 9/13;
		grid-row    : 3/4;
	}

	@media screen and (min-width : $break-desktop) {
		grid-row : 3/4;
	}
}
