	url('../fonts/AkzidenzGroteskPro-Regular.woff') format('woff'),
	url('../fonts/AkzidenzGroteskPro-Regular.ttf') format('truetype');
	font-weight  : 400;
	font-style   : normal;
	font-display : swap;
}

