.application {
	background-color : $green;
	padding          : 40px 0;

	@media screen and (min-width : $break-table) {
		padding-bottom : 60px;
		padding-top    : 128px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 80px;
		padding-top    : 185px;
	}
}

.application__wrapper {


	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
		margin          : 0 auto;
		max-width       : 840px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.application__left {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		width         : 35%;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.application__text {
	color         : $cream;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 22px;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 29px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 20px;
		line-height   : 32px;
		margin-bottom : 35px;
	}
}

.application__name {
	color         : $white;
	font-size     : 16px;
	font-weight   : 400;
	line-height   : 24px;
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 28px;
		line-height   : 38px;
		margin-bottom : 40px;
	}
}

.application__img {
	width : 50%;
}
