.contacts {
	padding : 100px 0 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 116px 0 60px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 136px 0 80px 0;
	}

}

.contacts__title {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}
}

.contacts__grid {


	@media screen and (min-width : $break-table) {
		display : flex;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.contacts__left {
	background-color : $dark-brown;
	margin           : 0 -16px;
	padding          : 32px 16px;

	@media screen and (min-width : $break-table) {
		margin    : 0;
		min-width : 372px;
		padding   : 28px 32px;
	}

	@media screen and (min-width : $break-desktop) {
		min-width : 640px;
		padding   : 50px 40px;
	}
}

.contacts-text {
	display : none;

	@media screen and (min-width : $break-table) {
		display     : block;
		margin-left : 32px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 70px;
	}

	li {
		border-bottom  : 1px solid $dark;
		color          : $dark;
		font-size      : 14px;
		font-weight    : 400;
		line-height    : 22px;
		padding-bottom : 14px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size      : 20px;
			line-height    : 32px;
			padding-bottom : 20px;
		}

		&:not(:last-child) {
			margin-bottom : 15px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 24px;
			}
		}
	}
}

.contacts__text {
	color         : $white;
	font-size     : 16px;
	font-weight   : 400;
	line-height   : 24px;
	margin-bottom : 18px;

	@media screen and (min-width : $break-table) {
		font-size     : 18px;
		line-height   : 24px;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 28px;
		line-height   : 38px;
		margin-bottom : 40px;
	}
}

.contacts__number {
	align-items : center;
	display     : flex;
}

.contacts__number {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 62px;
	}
}

.contacts__icons {
	align-items : center;
	display     : flex;
	gap         : 10px;

	@media screen and (min-width : $break-table) {
		gap : 20px;

	}

	@media screen and (min-width : $break-desktop) {

	}

	svg {
		height : 20px;
		width  : 20px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			height : 32px;
			width  : 32px;
		}
	}
}

.contacts__num {
	margin-left : 15px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 20px;
	}

	a {
		color       : $white;
		font-size   : 20px;
		font-weight : 400;
		line-height : 52px;

		@media screen and (min-width : $break-table) {
			font-size   : 24px;
			line-height : 32px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 36px;
			line-height : 32px;
		}

	}
}

.contacts__bottom {
	color       : $cream;
	font-size   : 12px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 32px;
	}
}
