.carsharing-alert {
	background-color : $dark-green;
	margin           : 0 -16px 40px -16px;
	padding          : 32px 16px;

	@media screen and (min-width : $break-table) {
		align-items     : flex-start;
		display         : flex;
		justify-content : space-between;
		margin          : 0 0 60px 0;
		padding         : 58px 55px;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 0 80px 0;
	}
}

.carsharing-alert__left {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		max-width     : 383px;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.carsharing-alert__text {
		color       : $white;
		font-size   : 18px;
		font-weight : 400;
		line-height : 26px;


		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 28px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 28px;
			line-height : 38px;
		}
	}
}


.carsharing-alert__right {


	@media screen and (min-width : $break-table) {
		margin-left : 43px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 86px;
	}


	.carsharing-alert__text {
		color       : $cream;
		font-size   : 14px;
		font-weight : 400;
		line-height : 22px;

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 20px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 20px;
			line-height : 32px;
		}

		p {
			&:not(:last-child) {
				margin-bottom : 17px;

				@media screen and (min-width : $break-desktop) {
					margin-bottom : 30px;
				}
			}
		}
	}
}


.carsharing__wrap {
	display               : grid;
	grid-row-gap          : 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap : 40px;
	}
}

.carsharing__img {
	grid-column : 6 span;
	margin      : 0 -16px;

	@media screen and (min-width : $break-table) {
		margin : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.carsharing__price {
	grid-column : 6 span;
}

.price--carsharing {
	background-color : $dark-brown;
	height           : 100%;
}
