.territory {
	background-color : $green;
	padding          : 40px 0 40px 0;


	@media screen and (min-width : $break-table) {
		padding : 60px 0 140px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 80px 0 204px 0;
	}
}

.territory__wrap {


	@media screen and (min-width : $break-table) {
		align-items     : end;
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.territory__left {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		width         : 53%;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.territory__right {


	@media screen and (min-width : $break-table) {
		margin-left : 49px;
		width       : 41%;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 91px;
	}
}

.territory__text {
	color         : $white;
	font-size     : 16px;
	font-weight   : 400;
	line-height   : 24px;
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		font-size     : 18px;
		line-height   : 26px;
		margin-bottom : 26px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 28px;
		line-height   : 38px;
		margin-bottom : 40px;
	}

	&--left {
		max-width : 600px;
	}

	&--right {
		font-size     : 14px;
		line-height   : 20px;
		margin-bottom : 0;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 24px;
			line-height : 38px;
		}
	}
}

.territory__title {
	color          : $white;
	font-family    : 'Phosphate';
	font-size      : 16px;
	font-style     : normal;
	font-weight    : 400;
	line-height    : 24px;
	margin-bottom  : 24px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size     : 20px;
		line-height   : 24px;
		margin-bottom : 30px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 28px;
		line-height   : 38px;
		margin-bottom : 40px;
	}
}

.territory__list {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 30px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}

	li {
		border-bottom  : 1px solid $white;
		color          : $white;
		font-size      : 14px;
		line-height    : 16px;
		padding-bottom : 14px;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			font-size      : 20px;
			line-height    : 24px;
			padding-bottom : 20px;
		}

		&:not(:last-child) {
			margin-bottom : 14px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 20px;
			}
		}
	}
}
