.base {
	.b-btn--brown {
		left       : 50%;
		margin-top : 40px;
		position   : relative;
		transform  : translateX(-50%);

		@media screen and (min-width : $break-table) {
			margin-top : 60px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-top : 80px;
		}

	}
}

.base__map {
	padding-bottom : 40px;

	@media screen and (min-width : $break-table) {
		padding-bottom : 60px;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 80px;
	}
}

.base__text {
	color       : rgba(54, 45, 54, 0.47);
	font-size   : 14px;
	font-weight : 400;
	line-height : 40px;
	margin-top  : 24px;
	text-align  : center;
}

.base__grid {

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		padding : 0 40px;
	}
}

.base-item {
	align-items    : center;
	border         : 1px solid #000;
	border-radius  : 20px;
	display        : flex;
	flex-direction : column;
	padding        : 24px 19px;

	@media screen and (min-width : $break-table) {
		flex-direction  : row;
		justify-content : space-between;
		padding         : 20px 20px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 30px 30px;
	}

	&:not(:last-child) {
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 30px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 40px;
		}
	}

	&--reverse {

		@media screen and (min-width : $break-table) {
			flex-direction : row-reverse;
		}


		.base-item__text {


			@media screen and (min-width : $break-table) {
				margin-left  : 23px;
				margin-right : 55px;
			}

			@media screen and (min-width : $break-desktop) {
				margin-left  : 42px;
				margin-right : 80px;
			}
		}
	}
}

.base-item__img {


}

.swiper-base--one {
	border-radius : 14px;
	margin-bottom : 30px;
	overflow      : hidden;
	width         : 100%;

	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		width         : 50%;
	}

	@media screen and (min-width : $break-desktop) {

	}

	.swiper-slide {
		border-radius : 14px;
		overflow      : hidden;
	}
}


.base-item__text {
	color       : $dark;
	font-size   : 16px;
	font-weight : 400;
	line-height : 24px;
	text-align  : center;
	@media screen and (min-width : $break-table) {
		font-size    : 20px;
		line-height  : 28px;
		margin-left  : 55px;
		margin-right : 23px;
		text-align   : unset;
		width        : 50%;
	}

	@media screen and (min-width : $break-desktop) {
		font-size    : 28px;
		line-height  : 38px;
		margin-left  : 84px;
		margin-right : 42px;

	}
}
