// -----------------------------------------------
//  Button config
// -----------------------------------------------

$btn-font-weight           : 400;
$btn-line-height           : 21px;
$btn-border-width          : 1px;
$btn-padding               : 16px 30px;
$btn-font-size             : 16px;
$btn-border-radius         : 8px;
$btn-transition            : background .3s ease, color .3s ease;

// OUTLINE

// SIZE
// Big
$btn-padding-big           : 14px 30px;
$btn-font-size-big         : 16px;
// Small
$btn-padding-small         : 8px 30px;
$btn-font-size-small       : 13px;

// COLOR
// White
$btn-white-bg              : transparent;
$btn-white-color           : #FFF;
$btn-white-bg-hover        : #FFF;
$btn-white-color-hover     : #0B081B;

// Brown
$btn-brown-bg              : #85331F;
$btn-brown-color           : #FFF;
$btn-brown-color-outline   : $btn-brown-bg;
$btn-brown-bg-hover        : darken(#85331F, 10%);
$btn-brown-color-hover     : #FFF;


// Link
$btn-link-bg               : transparent;
$btn-link-color            : #18A3FA;
$btn-link-bg-hover         : #F7F7F7;
$btn-link-color-hover      : darken(#18A3FA, 10%);

// Green
$btn-green-bg              : #7A8C6D;
$btn-green-color           : #FFF;
$btn-green-color-outline   : $btn-green-bg;
$btn-green-bg-hover        : darken($btn-green-bg, 10%);
$btn-green-color-hover     : #FFF;


// LOADING
$btn-loading-color         : #FFF;
$btn-loading-color-outline : #000;
// SPACING
$btn-spacing-x             : 5px;
$btn-spacing-y             : 5px;

// -----------------------------------------------
//  Button style
// -----------------------------------------------

.b-btn {
	align-items        : center;
	border             : $btn-border-width solid transparent;
	border-radius      : $btn-border-radius;
	-moz-box-sizing    : border-box;
	-webkit-box-sizing : border-box;
	box-sizing         : border-box;
	display            : inline-flex;
	flex-flow          : row nowrap;
	font-size          : 14px;
	font-weight        : $btn-font-weight;
	justify-content    : center;
	line-height        : 17px;
	padding            : $btn-padding;
	position           : relative;
	text-align         : center;
	text-decoration    : none;
	text-transform     : uppercase;
	transition         : $btn-transition;
	user-select        : none;
	vertical-align     : middle;
	white-space        : nowrap;


	@media screen and (min-width : $break-table) {
		font-size   : $btn-font-size;
		line-height : $btn-line-height;
	}

	&:hover,
	&:focus-within,
	&.focus {
		outline : 0;
	}

	&.disabled,
	&:disabled {
		background : inherit;
		box-shadow : none;
		cursor     : default;
		opacity    : .45;
	}

	&:active,
	&.active {

	}

	[class^="b-icon-"],
	[class*=" b-icon-"] {
		margin-right   : 10px;
		vertical-align : middle;
	}

	// SIZE
	// Big
	&--big {
		font-size : $btn-font-size-big;
		padding   : $btn-padding-big;
	}

	&--small {
		font-size : $btn-font-size-small;
		padding   : $btn-padding-small;
	}

	// COLOR
	// Brown
	&--brown {
		background-color : $btn-brown-bg;
		color            : $btn-brown-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-brown-bg-hover;
			color            : $btn-brown-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-brown-bg;
				color            : $btn-brown-color;
			}
		}
	}


	// Link
	&--link {
		background-color : $btn-link-bg;
		color            : $btn-link-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-link-bg-hover;
			color            : $btn-link-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-link-bg;
				color            : $btn-link-color;
			}
		}
	}

	// Green
	&--green {
		background-color : $btn-green-bg;
		color            : $btn-green-color;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-green-bg-hover;
			color            : $btn-green-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : $btn-green-bg;
				color            : $btn-green-color;
			}
		}
	}

	// White
	&--outline-white {
		background-color : transparent;
		border           : 1px solid #FFF;
		color            : #FFF;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $btn-white-bg-hover;
			color            : $btn-white-color-hover;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-white-bg;
			}
		}
	}

	&--outline-dark {
		background-color : transparent;
		border           : 1px solid $dark;
		color            : $dark;

		&:hover,
		&:focus-within,
		&:active,
		&.active {
			background-color : $dark-brown;
			color            : $white;
		}

		&.disabled,
		&:disabled {
			&:hover,
			&:focus-within,
			&:active,
			&.active {
				background-color : transparent;
				border-color     : $btn-white-bg;
			}
		}
	}

	// LOADING
	&--loading {
		&:before {
			animation        : loadingSpin 1s infinite linear;
			border           : 3px solid rgba($btn-loading-color, 0.6);
			border-radius    : 50%;
			border-top-color : $btn-loading-color;
			content          : '';
			display          : inline-block;
			height           : 12px;
			margin-left      : -15px;
			margin-right     : 10px;
			transition       : border .3s ease;
			vertical-align   : middle;
			width            : 12px;
		}

		&.outline {
			&:before {
				border           : 3px solid rgba($btn-loading-color-outline, 0.2);
				border-top-color : $btn-loading-color-outline;
			}

			&:hover {
				&:before {
					border           : 3px solid rgba($btn-loading-color, 0.6);
					border-top-color : $btn-loading-color;
				}
			}
		}
	}

	// SPACING
	& + .b-btn {
		margin-left : $btn-spacing-x;
	}
}

.b-btn-grouping {
	display : inline-block;

	.b-btn {
		border-radius : 0;
		float         : left;
		margin-right  : 0;

		&:first-child {
			border-radius : $btn-border-radius 0 0 $btn-border-radius;
		}

		&:last-child {
			border-radius : 0 $btn-border-radius $btn-border-radius 0;
		}

		&.outline {
			margin-left : -1px;
			z-index     : 1;

			&:hover {
				z-index : 2;
			}

			&.active {
				z-index : 3;
			}

			&:first-child {
				margin-left : 0;
			}
		}
	}
}

@keyframes loadingSpin {
	0% {
		transform : rotate(0deg);
	}
	100% {
		transform : rotate(360deg);
	}
}

// Future-proof disabling of clicks on `<a>` elements
a.b-btn.disabled,
fieldset[disabled] a.b-btn {
	pointer-events : none;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
	&.full-size {
		width : 100%;
	}
}
