.section {
	margin : 42px 0;
	@media screen and (min-width : $break-table) {
		margin : 62px 0;
	}
	@media screen and (min-width : $break-desktop) {
		margin : 105px 0;
	}
}

.section-title {
	color          : $brown;
	font-family    : 'Phosphate';
	font-size      : 30px;
	font-weight    : 400;
	line-height    : 40px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 36px;
		line-height : 48px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 55px;
		line-height : 67px;
	}

	&--cream {
		color : $cream;
	}

	&--white {
		color : $white;
	}
}

.subtitle {
	font-family    : 'Phosphate';
	font-size      : 16px;
	font-weight    : 400;
	line-height    : 24px;
	text-transform : uppercase;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 38px;
	}

	&--white {
		color : $white;
	}

	&--dark {
		color : $dark;
	}
}

.offset {
	padding : 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 60px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 80px 0;
	}

	&--second {
		padding : 100px 0 40px 0;

		@media screen and (min-width : $break-table) {
			padding : 116px 0 60px 0;
		}

		@media screen and (min-width : $break-desktop) {
			padding : 150px 0 80px 0;
		}
	}
}


.second-title {
	color       : $dark;
	font-size   : 16px;
	font-weight : 400;
	line-height : 32px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		line-height : 32px;
	}
}
