.food {
	overflow : hidden;
}

.food__grid {
	display               : grid;
	grid-row-gap          : 30px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 34px;
		grid-row-gap          : 40px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : 1200px) {
		grid-row-gap : 90px;
	}
}

.slider-main {
	grid-column : 1/5;

	@media screen and (min-width : $break-table) {
		grid-column : 1/6;
	}

	@media screen and (min-width : 1200px) {
		grid-column : 1/6;
		grid-row    : 1/5;
	}
}

.swiper--main {
	height : 100%;
}

.food__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	grid-column : 1/5;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		grid-column : 6/13;
		line-height : 32px;
	}

	@media screen and (min-width : 1200px) {
		grid-row : 1/4;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 24px;
		}
	}
}

.slider-thumb {
	grid-column : 1/5;
	grid-row    : 3/3;

	@media screen and (min-width : $break-table) {
		grid-column : 1/13;
		grid-row    : 3/3;
	}

	@media screen and (min-width : 1200px) {
		grid-column : 6/13;
		grid-row    : 4/4;
	}
}

.swiper--thumb {
	height   : 102px;
	overflow : visible;

	@media screen and (min-width : $break-table) {
		height   : 162px;
		margin   : 0 -9px;
		overflow : hidden;
	}

	@media screen and (min-width : 1200px) {

	}

	.swiper-slide {
		margin : 0 5px;
		width  : 45%;

		@media screen and (min-width : $break-table) {
			margin : 0 9px;
			width  : 27%;
		}

		@media screen and (min-width : 1200px) {
			width : 35%;
		}
	}
}
