.short {
	padding : 40px 0 80px;

	@media screen and (min-width : $break-table) {
		padding : 60px 0 60px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 80px 0 80px;
	}
}

.short__title {
	margin-bottom : 30px;


	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}
}

.short__grid {
	display               : grid;
	grid-gap              : 24px;
	grid-template-columns : repeat(3, 1fr);
	margin                : 0 -16px;

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(12, 1fr);
		margin                : 0;
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 40px;
	}
}

.short__image {
	grid-column : 3 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.short-price {
	border         : 1px solid #000;
	border-radius  : 13px;
	display        : flex;
	flex-direction : column;
	grid-column    : 3 span;
	height         : 100%;
	min-height     : 175px;
	padding        : 18px 16px;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		min-height : 285px;
		padding    : 30px 30px;
	}
}

.short-price__text {
	color       : $dark;
	font-size   : 14px;
	line-height : 20px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 24px;
	}
}

.short-price__bottom {
	align-items     : flex-end;
	display         : flex;
	justify-content : space-between;
	margin-top      : auto;
}

.short-price__time {
	color       : $dark;
	font-size   : 15px;
	font-weight : 400;
	line-height : 19px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 21px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 26px;
		line-height : 34px;
	}
}

.short-price__wrap {
	display  : flex;
	grid-gap : 15px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 30px;
	}
}

.short-price__name {
	color         : rgba(54, 45, 54, 0.7);
	font-size     : 10px;
	font-weight   : 400;
	line-height   : 13px;
	margin-bottom : 6px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 16px;
		line-height   : 21px;
		margin-bottom : 10px;
	}
}

.short-price__sum {
	color       : $brown;
	font-size   : 15px;
	font-weight : 400;
	line-height : 19px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 26px;
		line-height : 34px;
	}
}

.short-price__years {
	color       : $dark;
	font-size   : 10px;
	font-weight : 400;
	line-height : 13px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 16px;
		line-height : 21px;
	}
}
