.newbie-lab__grid {
	align-items           : center;
	display               : grid;
	grid-row-gap          : 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 32px;
		grid-row-gap          : 40px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap : 70px;
		grid-row-gap    : 60px;
	}
}

.newbie-lab__text {
	color       : $dark;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 32px;
	}
}

.newbie-lab__title {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 6/12;
		grid-row    : 2/2;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-lab__text--one {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 6/11;
		grid-row    : 3/5;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-lab__first {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/6;
		grid-row    : 1/7;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-lab__two {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 6/9;
		grid-row    : 5/8;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-lab__text--two {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 9/13;
		grid-row    : 5/8;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
