.alert-box--children {
	@media screen and (min-width : $break-table) {
		margin-top     : -88px;
		padding-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-top     : -130px;
		padding-bottom : 80px;
	}
}

.alert-box__wrap {
	background-color : $dark-green;
	margin           : 0 -16px;
	padding          : 32px 16px;

	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
		margin          : 0;
		padding         : 36px 34px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 55px 55px;
	}
}

.children-alert__text {

}

.alert-box__text--left {
	color         : $white;
	font-size     : 18px;
	font-weight   : 400;
	line-height   : 24px;
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		font-size     : 20px;
		line-height   : 28px;
		margin-bottom : 0;
		width         : 50%;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 38px;
	}
}

.alert-box__text--right {
	color       : $cream;
	font-size   : 14px;
	font-weight : 400;
	line-height : 22px;

	@media screen and (min-width : $break-table) {
		margin-left : 47px;
		width       : 40%;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 32px;
		margin-left : 126px;
	}
}


.alert-box--application {
	position : relative;
	z-index  : 2;

	@media screen and (min-width : $break-table) {
		margin-bottom : -64px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : -93px;
	}

	.alert-box__text--right {


		@media screen and (min-width : $break-table) {
			max-width : 228px;
			width     : 40%;
		}

		@media screen and (min-width : $break-desktop) {
			max-width : none;
		}
	}

	.alert-box__text--left {
		@media screen and (min-width : $break-table) {
			max-width : 354px;
			width     : 60%;
		}

		@media screen and (min-width : $break-desktop) {
			max-width : none;
			width     : 50%;
		}
	}
}
