:root {
	--app-height : 100%;
}

.header {
	left     : 0;
	padding  : 16px 18px;
	position : fixed;
	top      : 0;
	width    : 100%;
	z-index  : 100;

	@media screen and (min-width : $break-table) {
		padding : 20px 25px;
	}

	@media screen and (min-width : 1440px) {
		padding : 20px 45px;
	}

	&--dark {
		background-color : $dark;
	}

	&--cream {
		background-color : $cream;

		.nav__link {

			@media screen and (min-width : 1440px) {
				color : $dark;
			}
		}

		.burger {
			color : $dark;
		}
	}
}

.header__logo {
	flex-shrink : 0;
	height      : 42px;
	width       : 44px;

	@media screen and (min-width : $break-table) {
		height : 56px;
		width  : 60px;
	}

}

.header__container {
	align-items     : center;
	display         : flex;
	justify-content : space-between;
}

.header__panel {
	width : 100%;

	@media screen and (min-width : 1440px) {
		margin : 0 31px;
	}

	&.active {
		background-color : rgba(0, 0, 0, 0.6);
		bottom           : 0;
		left             : 0;
		position         : fixed;
		right            : 0;
		top              : 0;
		transition       : opacity 0.5s ease;
		z-index          : 100;
	}
}


.nav {
	padding-bottom : 30px;

	@media screen and (min-width : 1440px) {
		padding-bottom : 0;
	}

	&__list {
		display        : flex;
		flex-direction : column;
		width          : 100%;

		@media screen and (min-width : $break-table) {
			display : inline-flex;
		}

		@media screen and (min-width : 1440px) {
			flex-direction  : row;
			justify-content : space-between;
		}
	}

	&__item {
		&:not(:last-child) {
			margin-bottom : 16px;

			@media screen and (min-width : 1750px) {
				margin-bottom : 0;
			}
		}

		&:not(:first-child) {

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : 1440px) {
				margin-left : 12px;
			}
		}
	}

	&__link {
		color          : $white;
		font-size      : 12px;
		line-height    : 20px;
		position       : relative;
		text-transform : uppercase;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : 1750px) {

		}

		&:before {
			background-color : $white;
			bottom           : -2px;
			content          : "";
			height           : 1px;
			position         : absolute;
			right            : 0;
			transition       : width .3s ease;
			width            : 0;
			will-change      : width;
		}

		&:hover {
			&:before {
				left  : 0;
				right : unset;
				width : 100%;
			}
		}
	}
}

.header__wrap {
	align-items : center;
	display     : flex;

	.b-btn--outline-white {
		display     : none;
		font-size   : 12px;
		line-height : 15px;
		margin-left : 30px;

		@media screen and (min-width : $break-table) {
			display : inline-flex;

		}

		@media screen and (min-width : 1440px) {
			margin-left : 0;
		}

	}

	.b-btn--outline-dark {
		font-size   : 12px;
		line-height : 15px;
		margin-left : 30px;

		@media screen and (min-width : $break-table) {
			display     : inline-flex;
			margin-left : 30px;
		}

		@media screen and (min-width : 1440px) {
			margin-left : 0;
		}

	}
}

.header__menu {
	background-color : $dark;
	height           : 100svh;
	overflow-y       : auto;
	position         : absolute;
	right            : -150%;
	top              : 0;
	transition       : right .5s ease-in;
	width            : 100%;
	z-index          : 100;

	&.active {
		right : 0;
	}

	@media screen and (min-width : $break-table) {
		max-width : 280px;
	}

	@media screen and (min-width : 1440px) {
		background-color : transparent;
		height           : auto;
		max-width        : none;
		position         : unset;
		right            : unset;
	}
}

.header__wrapper {
	display        : flex;
	flex-direction : column;
	height         : 100%;
	padding        : 32px 16px;
	width          : 100%;

	@media screen and (min-width : $break-table) {
		padding : 40px 44px;
	}

	@media screen and (min-width : 1440px) {
		display  : block;
		overflow : visible;
		padding  : 0;
	}
}

.header__mobile {
	align-items     : flex-start;
	display         : flex;
	justify-content : space-between;
	margin-bottom   : 40px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 60px;
	}

	@media screen and (min-width : 1440px) {
		display : none;
	}
}

.header__bottom {
	border-top     : 1px solid rgba(255, 255, 255, 0.08);
	margin-top     : auto;
	padding-bottom : 36px;
	padding-top    : 34px;

	.b-btn--outline-white {
		width : 100%;

		@media screen and (min-width : $break-table) {
			display : none;
		}
	}

	@media screen and (min-width : 1440px) {
		display : none;
	}
}

.footer-contacts--menu {
	display         : flex;
	flex-wrap       : wrap;
	justify-content : space-between;
	margin-bottom   : 31px;
	max-width       : 340px;

	@media screen and (min-width : $break-table) {
		align-items   : flex-start;
		margin-bottom : 0;
		max-width     : none;
	}

	.footer-contacts__item {

		a {
			color       : $cream;
			font-size   : 14px;
			line-height : 24px;
		}

		&:not(:last-child) {

			@media screen and (min-width : $break-table) {
				margin-bottom : 20px;
			}
		}

		&:not(:first-child) {
			@media screen and (min-width : $break-table) {
				margin-left : 0;
			}
		}
	}

	.footer-contacts__name {
		font-size   : 8px;
		line-height : 10px;
	}
}

.menu-close {
	background-color : transparent;
	border           : none;
	height           : 24px;
	padding          : 0;
	width            : 24px;
}

.burger {
	background-color : transparent;
	border           : none;
	color            : $white;
	cursor           : pointer;
	flex-shrink      : 0;
	height           : 16px;
	padding          : 0;
	position         : relative;
	width            : 21px;

	@media screen and (min-width : 1440px) {
		display : none;
	}

	&::before,
	&::after {
		background-color : currentColor;
		content          : "";
		height           : 2px;
		left             : 0;
		position         : absolute;
		transition       : transform 0.3s ease-in-out, top 0.3s ease-in-out;
		width            : 100%;
	}

	&:after {
		left  : unset;
		right : 0;
		width : 75%;
	}

	&::before {
		top : 0;
	}

	&::after {
		top : calc(100% - 2px);
	}

	&__line {
		background-color : currentColor;
		border-radius    : 10px;
		height           : 2px;
		left             : 0;
		position         : absolute;
		top              : 50%;
		transform        : translateY(-50%);
		transition       : transform 0.3s ease-in-out;
		width            : 100%;
	}

}
