.newbie-food {
	background-color : $dark;
	
}

.newbie-food__grid {
	align-items           : center;
	display               : grid;
	grid-row-gap          : 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 40px;
		grid-row-gap          : 5px;
		grid-template-columns : repeat(12, 1fr);
		grid-template-rows    : repeat(12, 50px);
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap : 0;
		grid-row-gap    : 10px;
	}
}

.newbie-food__title {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/8;
		grid-row    : 1/3;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/8;
		grid-row    : 1/3;
	}
}

.newbie-food__text {
	color       : $cream;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 32px;
	}
}

.newbie-food__text--one {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/5;
		grid-row    : 4/8;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/4;
		grid-row    : 4/8;
	}
}

.newbie-food__first {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/5;
		grid-row    : 9/12;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/4;
		grid-row    : 10/12;
	}
}

.newbie-food__text--two {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 5/9;
		grid-row    : 9/12;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 5/8;
		grid-row    : 9/12;
	}
}

.newbie-food__two {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 5/9;
		grid-row    : 4/8;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 5/8;
		grid-row    : 4/8;
	}
}

.newbie-food__three {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 9/13;
		grid-row    : 1/10;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 9/13;
		grid-row    : 1/10;
	}
}

.newbie-food__text--three {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 9/13;
		grid-row    : 10/13;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 9/13;
		grid-row    : 10/13;
	}
}
