.world {
	background-color : $green;
}

.world__title {
	margin-bottom : 30px;
	max-width     : 259px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
		max-width     : 372px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 45px;
		max-width     : 582px;
	}
}

.world__grid {
	display               : grid;
	grid-gap              : 30px 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 24px 24px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 40px 40px;
	}
}

.world__item {
	border        : 1px solid $white;
	border-radius : 11px;
	padding       : 30px 30px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {

	}

	&:nth-of-type(1) {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 1/7;
			grid-row    : 1/3;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&:nth-of-type(2) {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 1/7;
			grid-row    : 3/3;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&:nth-of-type(4) {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 7/13;
			grid-row    : 1/2;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}

	&:nth-of-type(5) {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 7/13;
			grid-row    : 3/4;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.world__name {
	color         : $white;
	font-size     : 18px;
	font-weight   : 500;
	line-height   : 24px;
	margin-bottom : 18px;

	@media screen and (min-width : $break-table) {
		font-size     : 24px;
		line-height   : 32px;
		margin-bottom : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 38px;
	}
}

.world__text {
	color       : $cream;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 32px;
	}
}

.world__green {
	align-items      : center;
	background-color : $dark-green;
	border-radius    : 12px;
	display          : flex;
	grid-column      : 1/4;
	padding          : 20px 20px;

	@media screen and (min-width : $break-table) {
		grid-column : 7/13;
		grid-row    : 2/2;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 20px 30px;
	}
}

.world__img {
	flex-shrink : 0;
	height      : 37px;
	width       : 46px;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		height : 54px;
		width  : 67px;
	}
}

.world__text--bold {
	color       : $white;
	font-size   : 18px;
	font-weight : 400;
	line-height : 24px;
	margin-left : 15px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 24px;
		line-height : 32px;
		margin-left : 27px;
	}
}
