.group {
	background-color : $green;
}

.group__grid {
	display               : grid;
	grid-row-gap          : 30px;
	grid-template-columns : repeat(3, 1fr);


	@media screen and (min-width : $break-table) {
		grid-column-gap       : 24px;
		grid-row-gap          : 40px;
		grid-template-columns : repeat(12, 1fr);
	}

	
	@media screen and (min-width : $break-desktop) {
		grid-column-gap : 40px;
		grid-row-gap    : 60px;
	}
}

.group__title {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/7;
		grid-row    : 1/3;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.group__text {
	color       : $white;
	font-size   : 16px;
	font-weight : 400;
	grid-column : 1/4;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		grid-column : 7/13;
		grid-row    : 2/4;
		line-height : 28px;
		margin      : 0 auto;
		max-width   : 309px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 38px;
		max-width   : 431px;
	}
}

.travel-box {
	background-color : $dark-green;
	display          : flex;
	flex-direction   : column;
	height           : 100%;
	margin           : 0 -16px;

	@media screen and (min-width : $break-table) {
		margin : 0;
	}

	&--one {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 1/7;
			grid-row    : 3/6;
		}

		@media screen and (min-width : 1550px) {

		}
	}

	&--two {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 7/13;
			grid-row    : 4/6;
		}

		@media screen and (min-width : 1550px) {

		}
	}

	&--three {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 1/7;
		}

		@media screen and (min-width : 1550px) {

		}
	}

	&--four {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 7/13;
		}

		@media screen and (min-width : 1550px) {

		}
	}
}


.travel-box__img {
	height : 174px;

	@media screen and (min-width : $break-table) {
		height : 188px;
	}

	@media screen and (min-width : $break-desktop) {
		height : 304px;
	}
}

.travel-box__info {
	display        : flex;
	flex-direction : column;
	height         : 100%;
	padding        : 32px 16px;

	@media screen and (min-width : $break-table) {
		padding : 32px 19px;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 50px 50px;
	}
}

.travel-box__name {
	color          : #F2E9E1;
	font-size      : 15px;
	font-weight    : 400;
	line-height    : 24px;
	margin-bottom  : 16px;
	text-transform : capitalize;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 12px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 20px;
		line-height   : 32px;
		margin-bottom : 20px;
	}
}

.travel-box__text {
	color         : rgba(242, 233, 225, 0.7);
	font-size     : 12px;
	font-weight   : 400;
	line-height   : 16px;
	margin-bottom : 29px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 25px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 47px;
	}
}

.travel-box__table {
	margin-top : auto;
}

.price--travel {
	margin  : 0;
	padding : 0;
}

.price__table--travel {
	border-bottom  : 1px solid $cream;
	padding-bottom : 0;
	table-layout   : fixed;
	vertical-align : middle;

	td {
		padding        : 0;
		vertical-align : middle;
	}

	thead td {
		padding-bottom : 20px;
	}

	tbody td {
		border-bottom : none;
	}

	.price__text {
		max-width  : none;
		text-align : right;
	}

	.price__sum {
		font-size   : 15px;
		line-height : 19px;
		margin-top  : 0;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 21px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 26px;
			line-height : 34px;
		}
	}

	.price__name {
		color       : #D9D9D9;
		font-size   : 12px;
		line-height : 15px;

		@media screen and (min-width : $break-table) {
			font-size   : 13px;
			line-height : 15px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 20px;
			line-height : 24px;
		}
	}
}
