.alert-box--travel {

	@media screen and (min-width : $break-table) {
		margin-top : -97px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-top : -160px;
	}
}

.alert-box__text--left {
	p {

		@media screen and (min-width : $break-table) {
			max-width : 300px;
		}

		@media screen and (min-width : $break-desktop) {
			max-width : none;
		}

	}
}

.alert-box__text--right {
	p {

		@media screen and (min-width : $break-table) {
			max-width : 362px;
		}

		@media screen and (min-width : $break-desktop) {
			max-width : none;
		}
	}
}
