.home {
	display         : flex;
	flex-direction  : column;
	justify-content : center;
	min-height      : 100vh;
	overflow        : hidden;
	position        : relative;
}

.home__wrapper {
	height          : 100%;
	justify-content : center;
	overflow        : hidden;
	padding-bottom  : 40px;
	padding-top     : 100px;
	position        : relative;
	z-index         : 2;

	@media screen and (min-width : $break-table) {
		display         : flex;
		justify-content : space-between;
		margin          : 0 auto;
	}

	@media screen and (min-width : $break-desktop) {
		min-width : 990px;
	}
}

.home__title {
	font-family    : 'Phosphate';
	font-size      : 24px;
	font-style     : normal;
	font-weight    : 400;
	line-height    : 20px;
	margin-top     : 43px;
	text-align     : center;
	text-transform : uppercase;

	&--green {
		color : $green;
	}

	&--brown {
		color : $brown;
	}
}

.home__img {
	background-color : #FBFAF9;
	padding          : 22px 21px 41px 21px;


	@media screen and (min-width : $break-table) {
		padding : 25px 27px 50px 27px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.home__item {
	align-items    : center;
	display        : flex;
	flex-direction : column;

	&:not(:last-child) {
		margin-bottom : 32px;
	}

	@media screen and (min-width : $break-table) {
		&:not(:last-child) {
			margin-bottom : 0;
		}
	}

	@media screen and (min-width : $break-desktop) {

	}

	.b-btn {
		margin-top : 24px;
		min-width  : 180px;

		@media screen and (min-width : $break-table) {
			margin-top : 36px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}


.home__one {
	bottom   : -39px;
	height   : 170px;
	left     : -3px;
	position : absolute;
	width    : 66px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : 0;
		height : auto;
		left   : 0;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.home__two {
	bottom   : 35px;
	height   : 43px;
	position : absolute;
	right    : 5px;
	width    : 53px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : 82px;
		height : auto;
		right  : 33px;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.home__three {
	bottom   : 50%;
	height   : 28px;
	left     : 10px;
	position : absolute;
	width    : 47px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : 87px;
		height : auto;
		left   : 44%;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.home__four {
	bottom   : 48%;
	height   : 52px;
	position : absolute;
	right    : 12px;
	width    : 37px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		bottom : 67px;
		height : auto;
		right  : 44%;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.home__five {
	height   : 40px;
	left     : 76px;
	position : absolute;
	top      : 70px;
	width    : 40px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		height : auto;
		left   : 56px;
		top    : 185px;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {

	}

}

.home__six {
	height   : 40px;
	position : absolute;
	right    : 24px;
	top      : 76px;
	width    : 50px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		height : auto;
		left   : 50%;
		right  : unset;
		top    : 76px;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.home__seven {
	height   : 34px;
	position : absolute;
	right    : 0;
	top      : 20%;
	width    : 38px;
	z-index  : 1;

	@media screen and (min-width : $break-table) {
		height : auto;
		right  : 36px;
		top    : 258px;
		width  : auto;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.home__eight {
	left      : 50%;
	position  : absolute;
	top       : 50%;
	transform : translate(-50%, -50%);
	z-index   : 1;
}

.home__nine {
	bottom     : 0;
	height     : auto;
	left       : 0;
	max-height : none;
	object-fit : cover;
	position   : absolute;
	right      : 0;
	top        : 50%;
	transform  : translateY(-50%);
	width      : 100%;
	z-index    : 1;


	@media screen and (min-width : $break-table) {
		height    : 570px;
		left      : -123px;
		top       : 401px;
		transform : rotate(37deg);
		width     : 1280px;
	}

	@media screen and (min-width : 1200px) {
		height    : 100%;
		left      : 0;
		right     : unset;
		top       : 50%;
		transform : translateY(-50%) rotate(0);
		width     : 100%;
	}
}
