.newbie-sport {
	background-color : $green;
}

.newbie-sport__grid {
	align-items           : center;
	display               : grid;
	grid-row-gap          : 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-column-gap       : 30px;
		grid-row-gap          : 40px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-column-gap : 50px;
		grid-row-gap    : 60px;
	}
}

.newbie-sport__title {
	margin-bottom : 24px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 18px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 24px;
	}

}

.newbie-sport__text {
	color       : $cream;
	font-size   : 14px;
	font-weight : 400;
	line-height : 20px;

	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		line-height : 24px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 20px;
		line-height : 32px;
	}
}

.newbie-sport__text--one {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/6;
		grid-row    : 1/4;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-sport__first {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/6;
		grid-row    : 4/7;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-sport__two {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 6/9;
		grid-row    : 5/7;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-sport__three {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 8/13;
		grid-row    : 1/5;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.newbie-sport__text--two {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 9/13;
		grid-row    : 5/7;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
