.housing__grid {
	display               : grid;
	grid-gap              : 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		align-items           : center;
		grid-gap              : 40px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.housing__title {
	grid-column : 1/4;
	grid-row    : 1/1;

	@media screen and (min-width : $break-table) {
		grid-column : 1/6;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/5;
	}
}

.housing-contacts {
	background-color : $white;
	border-radius    : 12px;
	grid-column      : 1/4;
	grid-row         : 2/2;
	padding          : 24px 18px;

	@media screen and (min-width : $break-table) {
		grid-column : 7/13;
		grid-row    : 1/2;
		padding     : 30px 30px;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 1/7;
		grid-row    : 2/2;
	}
}

.housing-contacts__text {
	margin-bottom : 27px;

	p {
		color       : $dark;
		font-size   : 14px;
		font-weight : 400;
		line-height : 20px;


		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 18px;
			line-height : 27px;
		}
	}
}

.housing-contacts__icons {
	align-items : center;
	display     : flex;

	img {
		height : 20px;
		width  : 20px;
		@media screen and (min-width : $break-table) {
			height : 24px;
			width  : 24px;
		}

		@media screen and (min-width : $break-desktop) {
			height : 33px;
			width  : 33px;
		}

		&:not(:first-child) {
			margin-left : 12px;
		}
	}
}

.housing-contacts__number {
	align-items : center;
	display     : flex;

	a {
		color       : $dark;
		font-size   : 20px;
		font-weight : 400;
		line-height : 26px;
		margin-left : 10px;

		@media screen and (min-width : $break-table) {
			font-size   : 24px;
			line-height : 31px;
			margin-left : 12px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 32px;
			line-height : 41px;
			margin-left : 17px;
		}
	}
}

.housing-item {
	background-color : $dark-green;


	&--first {
		grid-column : 1/4;


		@media screen and (min-width : $break-table) {
			grid-column : 1/13;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 7/13;
			grid-row    : 1/3;
		}
	}

	&--two {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 1/13;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 1/7;
			grid-row    : 3/6;
		}
	}

	&--three {
		grid-column : 1/4;

		@media screen and (min-width : $break-table) {
			grid-column : 1/13;
		}

		@media screen and (min-width : $break-desktop) {
			grid-column : 7/13;
			grid-row    : 3/6;
		}
	}

}

.housing-item__footer {
	padding : 22px 22px;
}

.housing-item__row {
	display         : flex;
	flex-direction  : column;
	justify-content : space-between;
	min-height      : 168px;

	@media screen and (min-width : $break-table) {
		align-items    : center;
		flex-direction : row;
		min-height     : auto;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.housing-item__name {
	display        : flex;
	flex-direction : column;
	flex-shrink    : 0;

	@media screen and (min-width : $break-table) {
		align-items    : center;
		flex-direction : row;
	}

	@media screen and (min-width : $break-desktop) {

	}

	h4 {
		color       : $cream;
		font-size   : 16px;
		font-weight : 400;
		line-height : 21px;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 26px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.housing-item__icons {
	align-items : center;
	display     : flex;
	gap         : 3px;
	margin-top  : 12px;

	@media screen and (min-width : $break-table) {
		margin-left : 9px;
		margin-top  : 0;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.housing-item__text {
	color       : rgba(242, 233, 225, 0.7);
	font-size   : 12px;
	font-weight : 400;
	line-height : 15px;

	@media screen and (min-width : $break-table) {
		max-width : 135px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.housing-item__group {
	align-items : center;
	color       : $cream;
	display     : flex;
	flex-shrink : 0;
	font-size   : 16px;
	font-weight : 400;
	line-height : 21px;

	span {
		padding-left : 5px;
	}
}

.housing-item__about {
	display        : flex;
	flex-direction : column;
	gap            : 12px;

	@media screen and (min-width : $break-table) {
		align-items    : center;
		flex-direction : row;
		gap            : 23px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.housing-item__info {
	align-items     : center;
	display         : flex;
	gap             : 16px;
	justify-content : flex-end;

	@media screen and (min-width : $break-table) {
		gap             : 32px;
		justify-content : unset;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
