.children-about {

}

.children-about__wrap {


	@media screen and (min-width : $break-table) {
		align-items     : center;
		display         : flex;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.children-about__text {
	color         : $dark;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		font-size     : 14px;
		line-height   : 22px;
		margin-bottom : 0;
		margin-right  : 46px;
		width         : 50%;
	}

	@media screen and (min-width : $break-desktop) {
		font-size    : 20px;
		line-height  : 32px;
		margin-right : 76px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 16px;

			@media screen and (min-width : $break-table) {

			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 24px;
			}
		}
	}
}

.children-about__img {


	@media screen and (min-width : $break-table) {
		width : 50%;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
