.ecosystem {
	background-color : $green;
}

.ecosystem__title {
	color         : $white;
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 72px;
	}
}

.ecosystem__wrapper {
	display               : grid;
	grid-column-gap       : 50px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-template-columns : repeat(6, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-template-columns : repeat(12, 1fr);
	}
}

.ecosystem__left {
	grid-column   : 3 span;
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		grid-column   : 6 span;
		margin-bottom : 60px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 0;
	}

	svg {
		height     : 100%;
		max-height : none;
		object-fit : cover;
		width      : 100%;
	}
}

.ecosystem__right {
	grid-column : 3 span;

	@media screen and (min-width : $break-table) {
		grid-column : 6 span;
	}

	@media screen and (min-width : $break-desktop) {
		grid-column : 8/13;
	}
}

.ecosystem-card {
	background-color : $white;
	display          : flex;
	display          : none;
	flex-direction   : column;
	height           : 100%;
	justify-content  : space-between;
	padding          : 22px 22px;

	@media screen and (min-width : $break-table) {
		flex-direction : row;
		padding        : 27px 27px;
	}

	@media screen and (min-width : $break-desktop) {
		flex-direction : column;
		max-width      : 510px;
		padding        : 40px 40px;
	}

	&.active {
		display : flex;
	}
}

.ecosystem-card__img {
	flex-shrink   : 0;
	height        : 200px;
	margin-bottom : 22px;

	@media screen and (min-width : $break-table) {
		height        : 240px;
		margin-bottom : 0;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 40px;
	}

	img {
		height     : 100%;
		object-fit : cover;
		width      : 100%;
	}
}

.ecosystem-card__info {
	@media screen and (min-width : $break-table) {
		margin-left : 27px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-left : 0;
	}

	.b-btn--brown {
		font-size   : 14px;
		line-height : 18px;
		width       : 100%;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 21px;
			width       : auto;
		}
	}
}

.ecosystem-card__title {
	color         : $dark;
	font-size     : 22px;
	font-weight   : 500;
	line-height   : 28px;
	margin-bottom : 10px;
	max-width     : 220px;

	@media screen and (min-width : $break-table) {
		font-size     : 26px;
		line-height   : 32px;
		margin-bottom : 12px;
		max-width     : 240px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 36px;
		line-height   : 42px;
		margin-bottom : 18px;
		max-width     : 350px;
	}
}

.ecosystem-card__text {
	margin-bottom : 36px;

	p {
		color       : $dark;
		font-size   : 12px;
		font-weight : 400;
		line-height : 16px;

		@media screen and (min-width : $break-table) {
			font-size   : 14px;
			line-height : 18px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 18px;
			line-height : 24px;
		}
	}
}

.map {
	&__item {


		path {
			fill       : $dark;
			transition : fill .3s ease;
		}


		&.active {
			path {
				fill       : $cream;
				transition : fill .3s ease;
			}
		}

	}
}


.map__dot {
	cursor : pointer;

	&:hover {

		fill : $cream;

	}
}

