.han {
	padding : 40px 0 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 72px 0 60px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding : 80px 0 80px 0;
	}
}

.han__grid {
	align-items           : center;
	display               : grid;
	grid-gap              : 30px;
	grid-template-columns : repeat(4, 1fr);

	@media screen and (min-width : $break-table) {
		grid-gap              : 30px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-gap : 45px;
	}
}

.han__text {
	color       : $dark;
	font-size   : 16px;
	font-weight : 400;
	grid-column : 4 span;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 15px;
		line-height : 23px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 25px;
		line-height : 38px;
	}
}

.han__img {
	grid-column : 2/4;

	@media screen and (min-width : $break-table) {
		grid-column : 4 span;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
