.curator__grid {

	@media screen and (min-width : $break-table) {
		display         : flex;
		gap             : 20px;
		justify-content : space-between;
	}

	@media screen and (min-width : $break-desktop) {
		gap : 40px;
	}
}

.curator__title {
	max-width : 150px;
	@media screen and (min-width : $break-table) {
		max-width : 184px;
	}
}


.curator__left {
	margin-bottom : 40px;


	@media screen and (min-width : $break-table) {
		margin-bottom : 0;
		max-width     : 360px;
	}

}

.curator__img {
	margin-bottom : 40px;

	@media screen and (min-width : $break-table) {
		flex-shrink   : 0;
		height        : 100%;
		margin-bottom : 0;
		margin-top    : 142px;
		position      : sticky;
		top           : 113px;
		width         : 172px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 203px;
		width      : auto;
	}
}

.curator__right {

	@media screen and (min-width : $break-table) {
		max-width : 360px;
	}
}

.curator__title {
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 60px;
	}
}

.curator__text {
	color         : $dark;
	font-size     : 14px;
	font-weight   : 400;
	line-height   : 20px;
	margin-bottom : 30px;

	@media screen and (min-width : $break-table) {
		font-size     : 16px;
		line-height   : 24px;
		margin-bottom : 40px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size     : 20px;
		line-height   : 32px;
		margin-bottom : 60px;
	}

	p {
		&:not(:last-child) {
			margin-bottom : 24px;
		}
	}

	span {
		color       : $brown;
		font-size   : 14px;
		font-weight : 500;
		line-height : 20px;

		@media screen and (min-width : $break-table) {
			font-size   : 16px;
			line-height : 24px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 20px;
			line-height : 32px;
		}
	}

}

.curator__wrap {
	h3 {
		color         : #000;
		font-size     : 18px;
		font-weight   : 700;
		line-height   : 23px;
		margin-bottom : 24px;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 25px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 24px;
			line-height : 31px;
		}
	}

	p {
		color       : rgba(54, 45, 54, 0.7);
		font-size   : 18px;
		font-weight : 400;
		line-height : 24px;
	}
}
