.companies-slider {
	overflow : hidden;
	padding  : 40px 0;

	@media screen and (min-width : $break-table) {
		padding : 60px 0 100px 0;
	}

	@media screen and (min-width : $break-desktop) {
		padding-bottom : 147px;
		padding-top    : 80px;
	}
}

.swiper-wrap--companies {
	margin-bottom : 30px;

	@media screen and (min-width : $break-desktop) {
		margin-bottom : 40px;
	}
}

.swiper--companies {
	margin   : 0 -11.5px;
	overflow : visible;

	@media screen and (min-width : $break-table) {
		margin   : 0 -7px;
		overflow : hidden;
	}

	@media screen and (min-width : $break-desktop) {
		margin : 0 -10.5px;
	}

	.swiper-slide {
		margin : 0 4.5px;
		width  : 40%;

		@media screen and (min-width : $break-table) {
			margin : 0 7px;
			width  : 23%;
		}

		@media screen and (min-width : $break-desktop) {
			margin : 0 10.5px;
		}
	}
}
