// -----------------------------------------------
//  Form config
// -----------------------------------------------
// Error
$form-label-color  : #A7A8B1;
$form-label-size   : 12px;
$form-label-weight : 400;
$form-label-margin : 0 0 10px;
$form-error-color  : $red;
$form-error-size   : 13px;
$form-error-weight : 500;

// Hint
$form-hint-color   : #444;
$form-hint-size    : 12px;
$form-hint-weight  : 400;
$form-hint-style   : italic;
$form-hint-margin  : 5px 0 0;

// Grid
$form-grid         : 7px;

// -----------------------------------------------
//  Form style
// -----------------------------------------------

.form {
	.b-btn--green {
		min-width : 100%;

		@media screen and (min-width : $break-table) {

		}

		@media screen and (min-width : $break-desktop) {
			min-width : 321px;
		}
	}
}

.form--popup {
	input {
		border-bottom : 1px solid $dark;
		color         : $dark;

		&::placeholder {
			color : $dark;
		}
	}

	.b-btn--green {
		left      : 50%;
		position  : relative;
		transform : translateX(-50%);
	}
}

.form__row {
	margin-top : 14px;
	position   : relative;

	@media screen and (min-width : $break-table) {

	}

	@media screen and (min-width : $break-desktop) {
		margin-top : 24px;
	}


	&:first-child {
		margin-top : 0;
	}

	input[type="text"],
	input[type="email"],
	input[type="search"],
	input[type="number"],
	input[type="tel"],
	input[type="password"],
	select,
	textarea {
		vertical-align : top;
		width          : 100%;
	}
}

.form__input {
	border-bottom : 1px solid $white;
	position      : relative;
	z-index       : 1;
}

.form__hint {
	color       : $form-hint-color;
	font-size   : $form-hint-size;
	font-style  : $form-hint-style;
	font-weight : $form-hint-weight;
	margin      : $form-hint-margin;
	position    : relative;
	z-index     : 2;
}

.form__body {
	margin-bottom : 42px;
}

.form__error {
	color       : $form-error-color;
	font-size   : $form-error-size;
	font-weight : $form-error-weight;
	margin-top  : 3px;
	position    : relative;
	z-index     : 3;
}

.form-password {
	position : relative;

	input[type="text"],
	input[type="password"] {
		padding-right : 50px;
		position      : relative;
		z-index       : 1;
	}
}

.form-password__button {
	background                  : transparent;
	border                      : 0;
	bottom                      : 0;
	-moz-box-sizing             : border-box;
	-webkit-box-sizing          : border-box;
	box-sizing                  : border-box;
	cursor                      : pointer;
	opacity                     : .3;
	outline                     : 0;
	padding                     : 0;
	position                    : absolute;
	right                       : 0;
	-webkit-tap-highlight-color : transparent;
	top                         : 0;
	transition                  : opacity .3s ease;
	vertical-align              : middle;
	width                       : 42px;
	z-index                     : 10;

	i {
		position       : relative;
		vertical-align : middle;
		z-index        : 1;
	}

	&:after {
		background : #000;
		border-top : 1px solid #FFF;
		content    : '';
		display    : block;
		height     : 1px;
		left       : 9px;
		position   : absolute;
		top        : 24px;
		transform  : rotate(-30deg);
		transition : opacity .3s ease;
		width      : 24px;
		z-index    : 10;
	}

	@media screen and (min-width : $break-table) {

	}
	@media screen and (min-width : $break-desktop) {
		&:hover {
			opacity : 1;
		}
	}

	&.active {
		opacity : 1;

		&:after {
			opacity : 0;
		}
	}
}
