.bars {
	background-color : $green;
}


.bars__grid {
	display               : grid;
	grid-row-gap          : 30px;
	grid-template-columns : repeat(3, 1fr);

	@media screen and (min-width : $break-table) {
		grid-row-gap          : 40px;
		grid-template-columns : repeat(12, 1fr);
	}

	@media screen and (min-width : $break-desktop) {
		grid-row-gap : 60px;
	}
}

.bars__title {
	grid-column : 1/4;


	@media screen and (min-width : $break-table) {
		grid-column : 1/13;
		grid-row    : 1/1;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.bars__text {
	color       : $white;
	font-size   : 16px;
	grid-column : 1/4;
	line-height : 24px;

	@media screen and (min-width : $break-table) {
		font-size   : 20px;
		grid-column : 1/6;
		grid-row    : 2/4;
		line-height : 28px;
	}

	@media screen and (min-width : $break-desktop) {
		font-size   : 28px;
		line-height : 38px;
	}
}

.price--bars {
	grid-column : 1/4;
	grid-row    : 4/4;

	@media screen and (min-width : $break-table) {
		grid-column : 1/6;
		grid-row    : 5/8;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.bars__img--catch {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 7/13;
		grid-row    : 2/8;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.bars__img--yoda {
	grid-column : 1/4;


	@media screen and (min-width : $break-table) {
		grid-column : 1/6;
		grid-row    : 8/8;
	}

	@media screen and (min-width : $break-desktop) {

	}
}

.yoda {
	grid-column : 1/4;

	@media screen and (min-width : $break-table) {
		grid-column : 7/13;
		grid-row    : 8/8;
	}

	@media screen and (min-width : $break-desktop) {

	}

	&__top {
		margin-bottom : 8px;

		@media screen and (min-width : $break-table) {
			margin-bottom : 10px;
		}

		@media screen and (min-width : $break-desktop) {
			margin-bottom : 20px;
		}

		.yoda__text {
			margin-bottom : 3px;

			@media screen and (min-width : $break-table) {
			}

			@media screen and (min-width : $break-desktop) {
				margin-bottom : 10px;
			}
		}
	}

	&__text {
		color       : $white;
		font-size   : 16px;
		line-height : 24px;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 28px;
		}

		@media screen and (min-width : $break-desktop) {
			font-size   : 28px;
			line-height : 38px;
		}
	}

	&__bottom {
		.yoda__text {
			margin-bottom : 14px;

			@media screen and (min-width : $break-table) {
				margin-bottom : 24px;
			}

			@media screen and (min-width : $break-desktop) {

			}
		}
	}

	&__works {
		color       : $white;
		font-size   : 14px;
		line-height : 24px;

		@media screen and (min-width : $break-table) {
			font-size   : 20px;
			line-height : 38px;
		}

		@media screen and (min-width : $break-desktop) {

		}
	}
}

.bars__alert {
	border        : 1px solid #F2E9E1;
	border-radius : 4px;
	color         : $white;
	font-size     : 14px;
	grid-column   : 1/4;
	line-height   : 18px;
	padding       : 17px 23px;
	text-align    : center;


	@media screen and (min-width : $break-table) {
		font-size   : 16px;
		grid-column : 1/13;
		grid-row    : 9/9;
		line-height : 21px;
		padding     : 28px 36px;
	}

	@media screen and (min-width : $break-desktop) {

	}
}
